import "./index.css"
import { useState } from "react";

const Sidebar = ({ menuOption, setMenuOption, setIsCategory }) => {

    const [isOpen, setOpen] = useState(true)

    const trigSidebar = () => {
        setOpen(!isOpen);
    }

    const handleMenu = (index) => {
        let tempData = menuOption;
        setOpen(false);
        for(let i = 0; i < tempData.length; i++) {
            if(i === index) {
                tempData[i] = true;
            }
            else {
                tempData[i] = false;
            }
        }
        setMenuOption([...tempData])
        setIsCategory(false)
    }


    return <div className="sidebar" style={{
        width: isOpen ? '150px' : '60px'
    }}>
        <div className="button-group">
            <div onClick={trigSidebar} className='trig-btn mb-5 py-3 w-100 border-bottom'>
                <img className="p-2 ms-2" alt="" src="assets/ui/dashes.svg"></img>
            </div>
            <div onClick={() => handleMenu(0)} className='trig-btn w-100'>
                <img className="p-2 ms-2" alt=""  src="assets/ui/roomlayout.svg"></img>
                <div className="ml-2" style={{ display: (isOpen ? "" : "none") , color : "black"}} > Layout
                </div>
            </div>
            <div onClick={() => handleMenu(1)} className='trig-btn w-100'>
                <img className="p-2 ms-2" alt=""  src="assets/ui/roomelements.svg"></img>
                <div className="ml-2" style={{ display: (isOpen ? "" : "none") , color : "black"}} > Elements
                </div>
            </div>
            <div onClick={() => handleMenu(2)} className='trig-btn w-100'>
                <img className="p-2 ms-2" alt=""  src="assets/ui/bathroomproducts.svg"></img>
                <div className="ml-2" style={{ display: (isOpen ? "" : "none") , color : "black"}} > Products
                </div>
            </div>
            <div onClick={() => handleMenu(3)} className='trig-btn w-100'>
                <img className="p-2 ms-2" alt=""  src="assets/ui/bookconsultation.svg"></img>
                <div className="ml-2" style={{ display: (isOpen ? "" : "none") , color : "black"}} > Export Size
                </div>
            </div>
        </div>

    </div>
}

export default Sidebar;