import * as THREE from 'three';
import { AXIS, INIT_HEIGHT, OUTER_Z_LENGTH, PLANE} from '../Constant.js';

class Separation extends THREE.Mesh {
    place        = null;
    constructor(width, depth, position, place) {      
        const geometry = new THREE.PlaneGeometry( width, depth );
        const material = new THREE.MeshStandardMaterial( {             
            color: 0xd5d2d4,
            emissive: 0x111111,
            metalness: 1,
            roughness: 0.55,  
            side : THREE.DoubleSide 
        });
        super(geometry, material);
        this.position.set(position.x, position.y, position.z);
        this.place = place;
        if(place == PLANE.XY){
            this.rotation.x = 0 ;
        }
        if(place == PLANE.XZ){
            this.rotation.x = - Math.PI / 2 ;
        }
        if(place == PLANE.YZ){
            this.rotation.y = - Math.PI / 2 ;
        }
        this.userData.width = width;
        this.userData.depth = depth;
    }

    setPosOnAxis(scale, axis, value){
        switch(axis) {
            case AXIS.Z:
                this.scale.x = scale/this.userData.width * OUTER_Z_LENGTH;
                break;
            case AXIS.Y:
                this.scale.y = scale/this.userData.depth * INIT_HEIGHT;
                this.position.y = value;
                break;
        }
    }
}

export { Separation };