import * as THREE from 'three';
import { PLANE, COVER_DEPTH, AXIS, BASEBAR_WIDTH, INIT_HEIGHT, LOUVER_HEIGHT} from '../Constant.js';

class Louver extends THREE.Mesh {

    constructor(width, height, position, place) {  
        const space = 0.05;
        let shape = new THREE.Shape();
        shape.moveTo(-width/2, -height/2);
        shape.lineTo(-width/2, height/2);
        shape.lineTo(width/2, height/2);
        shape.lineTo(width/2, -height/2);
        shape.lineTo(-width/2, -height/2);
        let hole = new THREE.Shape();
        hole.moveTo(-width/2 + space, -height/2 +space);
        hole.lineTo(-width/2 + space, height/2 - space);
        hole.lineTo(width/2 - space, height/2 - space);
        hole.lineTo(width/2 - space, -height/2 + space);
        hole.lineTo(-width/2 + space, -height/2 + space);

        shape.holes.push( hole );

        const extrudeSettings = {
            steps: 1,
            depth: COVER_DEPTH,
            bevelEnabled: false,
        };
        const geometry = new THREE.ExtrudeGeometry( shape, extrudeSettings );
        const material = new THREE.MeshStandardMaterial( {             
            color: 0xffffff,
            emissive: 0x111111,
            metalness: 1,
            roughness: 0.55,  
        });
        super(geometry, material);
        this.position.set(position.x, position.y, position.z);
        

        let bar_height = 0.02;
        let extrudeSettings_bar = {
            steps: 1,
            depth: COVER_DEPTH,
            bevelEnabled: false,
        };
        const material_bar = new THREE.MeshStandardMaterial( {             
            color: 0x2f2f2f,
            emissive: 0x111111,
            metalness: 1,
            roughness: 0.55,  
        });
        for(let index = 0; index  <= ((height - 2 * space)/(2 * bar_height)); index++){
            let shape = new THREE.Shape();
            shape.moveTo(-width/2 + space, height/2 - space - (2 *index +1)* bar_height);
            shape.lineTo(width/2 - space, height/2 - space - (2 *index +1)* bar_height);
            shape.lineTo(width/2 - space, height/2 - space - (2 *index +2)* bar_height);
            shape.lineTo(-width/2 + space, height/2 - space - (2 *index +2)* bar_height);
            shape.lineTo(-width/2 + space, height/2 - space - (2 *index +1)* bar_height);
            let geometry = new THREE.ExtrudeGeometry( shape, extrudeSettings_bar );
            let mesh_bar = new THREE.Mesh( geometry, material_bar );
            mesh_bar.scale.y = 1/this.scale.y;
            this.add(mesh_bar);

        }

        this.place = place;
        this.userData.width = width;
        this.userData.depth = height;
    }

    scaleOnAxis(scale, axis){
        switch(axis) {
            case AXIS.X:
                this.scale.x =scale/this.userData.width * BASEBAR_WIDTH;
                break;
            case AXIS.Y:
                break;
        }
    }

    moveOnAxis(value, axis){
        switch(axis) {
            case AXIS.X:
                break;
            case AXIS.Z:
                this.position.z = value;
                break;
            case AXIS.Y:
                this.position.y= value;
                break;
        }
    }

    showElements(value){
        for(let index = 0 ; index < this.children.length; index++){
            this.children[index].material.visible = value;
        }
    }
}

export { Louver };