import * as THREE from 'three';
import { PLANE, COVER_DEPTH } from '../Constant.js';

class Compartment extends THREE.Group {

    constructor(width, height, depth,  position, place) {  
        super();
        this.position.set(position.x, position.y, position.z);
        const material = new THREE.MeshStandardMaterial( {             
            color: 0xffffff,
            emissive: 0x111111,
            metalness: 1,
            roughness: 0.55,  
            side : THREE.DoubleSide
        });

        let bottom_geo = new THREE.PlaneGeometry( width, depth );
        let bottom_mesh = new THREE.Mesh(bottom_geo, material);
        bottom_mesh.rotation.x = -Math.PI/2;
        this.add(bottom_mesh);
        let left_geo = new THREE.PlaneGeometry( depth, height );
        let left_mesh = new THREE.Mesh(left_geo, material);
        left_mesh.rotation.y = - Math.PI/2;
        left_mesh.position.x = - width/2;
        left_mesh.position.y = height/2;
        this.add(left_mesh);
        let right_geo = new THREE.PlaneGeometry( depth, height );
        let right_mesh = new THREE.Mesh(right_geo, material);
        right_mesh.rotation.y = - Math.PI/2;
        right_mesh.position.x =  width/2;
        right_mesh.position.y = height/2;
        this.add(right_mesh);
    }
}

export { Compartment };