import * as THREE from 'three';
import { Vector3 } from 'three';
import { AXIS, BAR_DEPTH, Bar_type, PLANE, Direction, Bar_Group } from "../Constant.js";
import { OuterBar } from "./OuterBar.js";

class ProfileSet  extends THREE.Group  {
    constructor(width, depth, material, position, place) {
        super();
        switch(place){
            case PLANE.YZ:
                this.add(new OuterBar(width, Bar_type.INNER, material, new Vector3(position.x, position.y + depth/2 + BAR_DEPTH/2, position.z), AXIS.Z, Direction.MIDDLE, PLANE.XZ, Bar_Group.SET));
                this.add(new OuterBar(width, Bar_type.INNER, material, new Vector3(position.x, position.y - depth/2 - BAR_DEPTH/2, position.z), AXIS.Z, Direction.MIDDLE, PLANE.XZ, Bar_Group.SET));
                this.add(new OuterBar(depth, Bar_type.INNER, material, new Vector3(position.x, position.y, position.z + width/2 + BAR_DEPTH/2), AXIS.Y, Direction.MIDDLE,  PLANE.XY, Bar_Group.SET));
                this.add(new OuterBar(depth, Bar_type.INNER, material, new Vector3(position.x, position.y, position.z - width/2 - BAR_DEPTH/2), AXIS.Y, Direction.MIDDLE, PLANE.XY, Bar_Group.SET));
                break;
            case PLANE.XY:
                 break;
            case PLANE.XZ:
                break;
            default:
                break;
        }
        this.place = place;
        this.userData.width = width;
        this.userData.depth = depth;
    }




}

export { ProfileSet };