import * as THREE from 'three';
import { AXIS, Direction, PLANE, BASEBAR_HEIGHT, BAR_DEPTH, Bar_type } from '../Constant.js';

class Bar extends THREE.Mesh {

    axis        = null;
    direction   = null;

    constructor(material, geometry, position, axis, direction) {
        super(geometry, material);

        this.position.set(Number((position.x).toFixed(4)), Number((position.y).toFixed(4)), Number((position.z).toFixed(4)));
        this.axis = axis;
        this.direction = direction;
        this.castShadow = true;
        this.receiveShadow = true;
    }

    scaleAxis(value) {
        this.scale.y = value;
    }

    moveAxis(pos, axis) {
        let value = Number(pos.toFixed(4));
        switch(this.axis) {
            case AXIS.X:
                if(axis === AXIS.Y){
                    if(this.place === PLANE.XY)
                        this.position.y = value;
                }else
                    this.position.z = (this.direction === Direction.START) ? value : -value;
                break;
            case AXIS.Y:
                if(this.type != Bar_type.INNER){
                    if(axis === AXIS.X) this.position.x = (this.position.x > 0 ) ? value : -value;
                }
                if(axis === AXIS.Z) this.position.z = (this.position.z> 0) ? value : -value;
                if(axis === AXIS.Y) this.position.y = value;
                break;
            case AXIS.Z:
                if(axis === AXIS.Y){
                    if(this.place === PLANE.XZ && this.position.y != (BASEBAR_HEIGHT/2 + BAR_DEPTH/2))
                        this.position.y = value;
                }else
                    if(this.type != Bar_type.INNER){
                         this.position.x = (this.position.x > 0 ) ? value : -value;
                    }
                break;
        }
    }
}

export { Bar };