import * as THREE from 'three';
import { Vector3 } from 'three';
import { AXIS, BASEBAR_DEPTH, BASEBAR_WIDTH, OUTER_Z_LENGTH, INIT_Y_DOWN, BAR_DEPTH, INIT_HEIGHT, BASEBAR_HEIGHT, PLANE, COVER_DEPTH } from '../Constant.js';
import { SceneController } from '../SceneController.js';

class Arrow extends THREE.ArrowHelper {

    axis = null;

    constructor(axis) {
        let direction;
        switch(axis) {
            case AXIS.X:
                direction = new Vector3(1, 0, 0);
                break;
            case AXIS.Y:
                direction = new Vector3(0, 1, 0);
                break;
            case AXIS.Z:
                direction = new Vector3(0, 0, 1);
                break;
        }
        direction.normalize();

        const origin = direction;
        const length = 0.3;
        const hex = 0x000000;

        super(direction, origin, length, hex);
        this.axis = axis;
    }

    OnArrowObjectChanged() {
        const sceneController = SceneController.instance();
        sceneController.hideHover();
        let updatedScale = 0;
        switch(this.axis) {
            case AXIS.X:
                updatedScale= sceneController.baseFrame.scale.x * this.position.x;
                break;
            case AXIS.Y:
                updatedScale= sceneController.baseFrame.scale.y * this.position.y;
                break;
            case AXIS.Z:
                updatedScale= sceneController.baseFrame.scale.z * this.position.z;
                break;
        }

        if(updatedScale > 2.2)
            updatedScale= 2.2;
        else if (updatedScale < 0.4)
            updatedScale = 0.4;

        for(let index = 0; index < sceneController.baseFrame.children.length; index++) {
            let basebar = sceneController.baseFrame.children[index];
            
            if(basebar.axis === this.axis) {
                basebar.scaleAxis(updatedScale);
            }

            if(basebar.axis === AXIS.Z - this.axis) {
                let width = (basebar.axis === AXIS.Z) ? BASEBAR_WIDTH : BASEBAR_WIDTH / 2;
                let updated_pos = (this.axis === AXIS.X) ? width * updatedScale / 2 - BASEBAR_DEPTH / 2 : width * updatedScale / 2 + BASEBAR_DEPTH / 2
                basebar.moveAxis(updated_pos);
            }
        }

        let z_updatescale = (BASEBAR_WIDTH / 2 *updatedScale + 2 * BAR_DEPTH )/OUTER_Z_LENGTH;
        for(let index = 0; index < sceneController.inner_Group.length; index++) {
            let outerbar = sceneController.inner_Group[index];
            
            if(outerbar.axis === this.axis) {
                if(this.axis === AXIS.Z){
                    outerbar.scaleAxis(z_updatescale / outerbar.userData.length * OUTER_Z_LENGTH);
                }else if(this.axis === AXIS.X){
                    outerbar.scaleAxis(updatedScale / outerbar.userData.length * BASEBAR_WIDTH);
                }else if(this.axis === AXIS.Y){
                    outerbar.scaleAxis(updatedScale / outerbar.userData.length * INIT_HEIGHT);
                }
            }

            if(this.axis != AXIS.Y){
                if(outerbar.axis != this.axis){
                    let width = (this.axis === AXIS.X) ? BASEBAR_WIDTH : OUTER_Z_LENGTH;
                    outerbar.moveAxis(width * updatedScale / 2 - BAR_DEPTH / 2, this.axis);
                }
            }else{
                if(outerbar.axis === AXIS.Y)
                    outerbar.moveAxis(INIT_HEIGHT * updatedScale / 2 + INIT_Y_DOWN, this.axis);
                else
                    outerbar.moveAxis(INIT_HEIGHT * updatedScale  + INIT_Y_DOWN + BAR_DEPTH/2, this.axis);
            }
            outerbar.drawDots(outerbar.scale.y * outerbar.userData.length);
        }

        for(let index = 0; index < sceneController.separation_group.children.length; index ++){
            let separate = sceneController.separation_group.children[index];
            let pos_value;
            if(this.axis == AXIS.Y){
                pos_value = INIT_HEIGHT * updatedScale / 2 + INIT_Y_DOWN;
            }
            separate.setPosOnAxis(updatedScale, this.axis, pos_value);
        }

        for(let index = 0; index < sceneController.covers.children.length; index++){
            let cover = sceneController.covers.children[index];
            let pos_value;
            switch (this.axis){
                case AXIS.X:
                    pos_value = BASEBAR_WIDTH * updatedScale / 2 - BAR_DEPTH / 2 + COVER_DEPTH;
                    if(cover.position.x < 0)
                        pos_value = - pos_value;
                    break;
                case AXIS.Y:
                    if(cover.place != PLANE.XZ)
                        pos_value = INIT_HEIGHT * updatedScale / 2 + INIT_Y_DOWN;
                    else
                        pos_value = INIT_HEIGHT * updatedScale  + INIT_Y_DOWN + BAR_DEPTH/2 + COVER_DEPTH;
                    break;
                case AXIS.Z:
                    pos_value = OUTER_Z_LENGTH * updatedScale / 2 - BAR_DEPTH / 2 + COVER_DEPTH;
                    break;
                default:
                    break;
            }
            cover.moveOnAxis(pos_value, this.axis);
            cover.scaleOnAxis(updatedScale, this.axis);
        }
        let pos_value;
        switch(this.axis){
            case AXIS.X:
                break;
            case AXIS.Y:
                pos_value = INIT_HEIGHT * updatedScale / 2 + INIT_Y_DOWN;
                break;
            case AXIS.Z:
                pos_value = OUTER_Z_LENGTH * updatedScale / 2 - BAR_DEPTH / 2 + COVER_DEPTH;
                break;
            default:
                break;
        }
        for(let index = 0; index < sceneController.doors.children.length; index++){
            let door = sceneController.doors.children[index];
            door.moveOnAxis(pos_value, this.axis);
            door.scaleOnAxis(updatedScale, this.axis);
        }
        sceneController.showProductSize();
        sceneController.orbit.enabled = false;
    }
}

export { Arrow };