import * as THREE from 'three';
import { PLANE } from '../Constant.js';

class Plane extends THREE.Mesh {

    place        = null;

    constructor(width, depth, position, place) {      
        const geometry = new THREE.PlaneGeometry( width, depth );
        const material = new THREE.MeshBasicMaterial( { color : 0x00FFFFFF,   visible: false, side : THREE.DoubleSide });
        super(geometry, material);
        this.position.set(position.x, position.y, position.z);
        this.place = place;

        if(place == PLANE.XY){
            this.rotation.x = 0 ;
        }
        if(place == PLANE.XZ){
            this.rotation.x = - Math.PI / 2 ;
        }
        if(place == PLANE.YZ){
            this.rotation.y = - Math.PI / 2 ;
        }
        this.userData.width = width;
        this.userData.depth = depth;
    }

}

export { Plane };