

import { SceneController } from './SceneController.js'
import React, { useState, useEffect } from 'react';

import Navbar from "./component/Navbar"
import Sidebar from "./component/Sidebar"

function App() {

    useEffect(() => {
        animate();
        addComponent();
        window.addEventListener('resize', OnWindowResize);
        var canvas = document.getElementById("canvas-container");
        canvas.addEventListener('pointermove', onPointerMove);
        canvas.addEventListener('pointerdown', onPointerDown);
        document.addEventListener('keydown', onDocumentKeyDown);
        canvas.addEventListener('pointerup', onPointerUp);

        var export_csv = document.getElementById("Export_csv");
        export_csv.addEventListener('click', onExportCSV);
    }, []);


    const animate = () => {
   //     requestAnimationFrame(animate);
        SceneController.instance().orbit.update();
        SceneController.instance().render();
    }

    const OnWindowResize = (event) => {
        SceneController.instance().resize();
    }

    const addComponent = () => {
        SceneController.instance().addComponent();
    }

    const onPointerMove = (event) => {
        SceneController.instance().onPointerMove(event);
    }

    const onPointerDown = (event) => {
        SceneController.instance().onPointerDown(event);
    }

    const onDocumentKeyDown = (event) => {
        SceneController.instance().onDocumentKeyDown(event);
    }

    const onPointerUp = (event) => {
        SceneController.instance().onPointerUp(event);
    }

    const onExportCSV = (event) => {
        SceneController.instance().onExportSize(event);
    }

    const [menuOption, setMenuOption] = useState([false, false, false, false]);
    const [isCategory, setIsCategory] = useState(false);

    return (
        <div className='container vh-100 overflow-auto'>
            <div className='row'>
                <Navbar />
                <Sidebar
                    menuOption={menuOption}
                    setMenuOption={setMenuOption}
                    setIsCategory={setIsCategory}
                />
            </div>
            <div className='row content'>
                <div className="roomsSideBar" style={{ marginLeft: (menuOption[0] && !isCategory ? 0 : -400) }}>
                    <h6 className='trig-btn  py-3 w-100 border-bottom' style={{ color: "black" }}>  Layout</h6>
                </div>
                <div className='roomsSideBar' style={{ marginLeft: (menuOption[1] && !isCategory ? 0 : -400) }} >
                    <h6 className='trig-btn  py-3 w-100 border-bottom' style={{ color: "black" }}> Elements</h6>
                    <div>
                        <h6>Add Inner Bar</h6>
                        <select id = "inner_axis"> 
                            <option value= "inner_z_axis"> Inner Z-Axis  bar</option>
                            <option value= "inner_y_axis"> Inner Y-Axis  bar</option>
                            <option value= "inner_x_axis"> Inner X-Axis  bar</option>
                        </select>
                    </div>
                    <div>
                        <h6>Add Double Bar</h6>
                        <select id = "double_axis"> 
                            <option value= "double_z_axis"> Double Z-Axis  bar</option>
                            <option value= "double_y_axis"> Double Y-Axis  bar</option>
                            <option value= "double_x_axis"> Double X-Axis  bar</option>
                        </select>
                    </div>
    
                    <div>
                        <button id="Add_yz_set" >Add Inner Set</button>
                    </div>

                    <button id="Add_separation_yz" >Add Separation_YZ</button>
                    <button id="Add_separation_xz" >Add Separation_XZ</button>
                    <button id="Add_separation_xy" >Add Separation_XY</button>
                    <div>
                        <button id="Add_cover_yz" >Add Cover_YZ</button>
                    </div>
                    <div>
                        <button id="Add_cover_xz" >Add Cover_XZ</button>
                    </div>
                    <div>    
                        <button id="Add_cover_xy" >Add Cover_XY</button>
                    </div>
                </div>
                <div className='roomsSideBar' style={{ marginLeft: (menuOption[2] ? 0 : -400) }} >
                    <h6 className='trig-btn py-3 w-100 border-bottom' style={{ color: "black" }}>  Products</h6>
                    <div>
                        <button id="Add_door" >Add Door</button>
                    </div>

                    <button id="Add_cablebox" >Add Cable Box</button>
                    <div>
                        <button id="Add_louver" >Add Louver</button>
                    </div>

                    <button id="Add_Compartment" >Add Compartment</button>
                </div>
                <div className='roomsSideBar' style={{ marginLeft: (menuOption[3] ? 0 : -400) }} >
                    <h6 className='trig-btn py-3 w-100 border-bottom' style={{ color: "black" }}>  Export Size</h6>
                    <button id ="Export_csv" >Export Size</button>
                </div>

                <div id="canvas-container" className='border col-12'>
                </div>
            </div>

        </div>
    );
}

export default App;
