import * as THREE from 'three';
import { AXIS, BASEBAR_DEPTH, BASEBAR_HEIGHT, BASEBAR_WIDTH, Direction } from "../Constant.js";
import { Bar } from './Bar.js';

class BaseBar extends Bar {
    constructor(material, position, axis, direction) {
        const length = (axis === AXIS.X) ? BASEBAR_WIDTH : BASEBAR_WIDTH / 2;
        const geometry = new THREE.BoxGeometry(BASEBAR_HEIGHT, length, BASEBAR_DEPTH);

        super(material, geometry, position, axis, direction);
        this.rotation.z = -Math.PI / 2;
        this.rotation.y = (axis === AXIS.X) ? 0 : -Math.PI / 2 ;
        this.userData.length = length;
    }
}

export { BaseBar };