import * as THREE from 'three';
import { Vector3 } from 'three';

import {  OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { TransformControls }  from 'three/examples/jsm/controls/TransformControls'

import { GUI } from 'three/examples/jsm/libs/lil-gui.module.min.js';

import { Arrow } from './component/Arrow.js';
import { BaseBar } from './component/BaseBar.js';
import { OuterBar } from './component/OuterBar.js';

import { ProfileSet } from './component/ProfileSet.js';
import { Plane } from './component/Plane.js';
import { Separation } from './component/Separation.js';
import { Cover } from './component/Cover.js';
import { Door } from './component/Door.js';
import { CableBox } from './component/CableBox.js';
import { Louver } from './component/Louver.js';
import { Compartment } from './component/Compartment.js';
import { AXIS, Direction, BAR_DEPTH, PLANE, DELTA_DOT,   Bar_type, Bar_Group, COVER_DEPTH, OUTER_Z_LENGTH, INIT_HEIGHT, INIT_Y_DOWN, BASEBAR_HEIGHT, BAR_STATUS, OUTERBAR_WIDTH, INNERBAR_WIDTH, BASEBAR_WIDTH } from './Constant.js';

import {RayysLinearDimension} from './component/RayysLinearDimension.js';


class SceneController {

    static _instance    = null;

    renderer            = null;
    scene               = null;
    camera              = null;
    orbit               = null;
    canvas              = null;
    container           = null;

    product             = null;
    baseFrame           = null;
    outer_Frame         = null;
    separation_group    = new THREE.Group();
    covers              =  new THREE.Group();
    doors               = new THREE.Group();
    louvers             = new THREE.Group();;


    planeGroup          = null;
    pro_width           = null;
    pro_depth           = null;

    pointer             = null;
    raycaster           = null;
    inner_type          = null;
    inner_Group         = [];
    profile_set         = null;

    hoverbar            = null;
    bar_length          = null;
    bar_center          = null;
    innerhovered        = null;
    innerselected       = null;
    selected_center     = new THREE.Vector3();

    isPointerDown       = false;
    boxSpace            = null;
    hoverset            = null;
    hoverseperate       = null;
    hovercablebox        = null;
    hovercompartment    = null;
    hoverdoor           = null;
    hoverlouver         = null;
    hovercover          = null;

    Pro_x               = null;
    Pro_y               = null;
    Pro_z               = null;

    Bar_len             = null;
    from_dis            = null;
    to_dis              = null;

    start_bar           = null;
    end_bar             = null;
    from_bar            = null;
    to_bar              = null;

    arrow_x_control     = null;
    arrow_y_control     = null;
    arrow_z_control     = null;

    gui                 = null;

    bar_set             = null;
    dou_bar             = null;

    static instance() {
        if(SceneController._instance === null)
            SceneController._instance = new SceneController();
        return SceneController._instance;
    } 

    constructor() {
        this.initRenderer();
        this.initScene();
        this.initCamera();
        this.initOrbit();
        this.initBaseFrame();
        this.initOuterFrame();
        this.initArrow();
        this.showProductSize();
    }

    initRenderer() {
        this.canvas = document.createElement('canvas');
        this.container = document.getElementById('canvas-container');
        this.renderer = new THREE.WebGLRenderer({canvas: this.canvas, antialias: true});
        this.renderer.setPixelRatio(window.devicePixelRatio);
        this.renderer.shadowMap.enabled = true;
        this.renderer.setSize(this.container.clientWidth, this.container.clientHeight);
        

        this.container.append(this.renderer.domElement);
    }

    initScene() {
        this.scene = new THREE.Scene();
        this.scene.background = new THREE.Color(0x7986f7);
        this.scene.fog = new THREE.Fog(0xffffff, 10, 1500);
        
        const upLight = new THREE.PointLight(0xf0ffff, 2, 50)
        upLight.position.set( 0, 5, -3 ); //default; light shining from top
        this.scene.add(upLight);
        const frontLight1 = new THREE.PointLight(0xf0ffff, 0.5, 50);
        frontLight1.position.set(-15, 1, 15);
        this.scene.add(frontLight1);
        const frontLight2 = new THREE.PointLight(0xf0ffff,  0.5, 50);
        frontLight2.castShadow = true; // default false
        frontLight2.shadow.mapSize.width = 1; // default
        frontLight2.shadow.mapSize.height = 1; // default
        frontLight2.shadow.camera.near = 0.5; // default
        frontLight2.shadow.camera.far = 500; // default
        frontLight2.position.set(20, 0.5, 15);
        this.scene.add(frontLight2);
        const frontLight3 = new THREE.PointLight(0xf0ffff,  0.5, 50);
        frontLight3.position.set(0, 0.5, 30);
        this.scene.add(frontLight3);
        const sideLight1 = new THREE.PointLight(0xf0ffff, 0.3, 50);
        sideLight1.position.set(-15, 0.5, -15);
        this.scene.add(sideLight1);
        const sideLight2 = new THREE.PointLight(0xf0ffff, 0.3, 50);
        sideLight2.position.set(15, 0.5, -10);
        this.scene.add(sideLight2);
        const backLight1 = new THREE.PointLight(0xf0ffff, 0.3, 50);
        backLight1.position.set(5, 0.5 -20);
        this.scene.add(backLight1);

        let plane_geo = new THREE.PlaneGeometry(100, 100);
        let plane_mat = new THREE.MeshBasicMaterial( { color : 0x7986f7, side : THREE.DoubleSide });
        let plane_mesh = new THREE.Mesh(plane_geo, plane_mat);
        plane_mesh.position.y = -BASEBAR_HEIGHT/2;
        plane_mesh.rotation.x = - Math.PI/2;
        this.scene.add(plane_mesh);
        
    }

    initCamera() {
        this.camera = new THREE.PerspectiveCamera(1, this.container.clientWidth / this.container.clientHeight, 1, 1000);
        this.camera.position.set(-100, 80,150);
        this.camera.lookAt(0, 1, 0);
    }

    initOrbit() {
        this.orbit = new OrbitControls(this.camera, this.renderer.domElement);
        this.orbit.listenToKeyEvents(window);
        this.orbit.enableDamping = true;
        this.orbit.dampingFactor = 0.05;
        this.orbit.target.set(0, 1, 0);
        this.orbit.screenSpacePanning = false;
        this.orbit.minDistance = 10;
        this.orbit.maxDistance = 300;
        this.orbit.maxPolarAngle = Math.PI / 2; 
    }

    initBaseFrame() {
        if(this.product === null)
            this.product = new THREE.Group();
        if(this.baseFrame === null)
            this.baseFrame = new THREE.Group();

        const material = new THREE.MeshStandardMaterial({
            color: 0x1c2722,
            emissive: 0x111111,
            metalness: 1,
            roughness: 0.55,
        });

        this.baseFrame.add(new BaseBar(material, new Vector3(0, 0, 0.275), AXIS.X, Direction.START));
        this.baseFrame.add(new BaseBar(material, new Vector3(0, 0, -0.275), AXIS.X, Direction.END));
        this.baseFrame.add(new BaseBar(material, new Vector3(0.475, 0, 0), AXIS.Z, Direction.END));
        this.baseFrame.add(new BaseBar(material, new Vector3(-0.475, 0, 0), AXIS.Z, Direction.START));
        this.scene.add(this.product);
        this.product.add(this.baseFrame);
    }

    initOuterFrame() {
        if(this.outer_Frame === null)
            this.outer_Frame = new THREE.Group();
        
        this.pro_width = this.getSize(this.baseFrame).width;
        this.pro_depth = this.getSize(this.baseFrame).depth;

        const material = new THREE.MeshStandardMaterial({
            color: 0xd5d2d4,
            emissive: 0x111111,
            metalness: 1,
            roughness: 0.55,
        });

        let yBar_pos_y = INIT_Y_DOWN + INIT_HEIGHT/2;
        let xBar_down_pos_x = INIT_Y_DOWN - BAR_DEPTH/2;
        let xBar_up_pos_x = INIT_Y_DOWN + INIT_HEIGHT + BAR_DEPTH/2;
        this.outer_Frame.add(new OuterBar(this.pro_width, Bar_type.OUTER, material, new Vector3(0, xBar_down_pos_x, 0.2875), AXIS.X, Direction.START));
        this.outer_Frame.add(new OuterBar(this.pro_width, Bar_type.OUTER, material, new Vector3(0, xBar_down_pos_x, -0.2875), AXIS.X, Direction.END));
        this.outer_Frame.add(new OuterBar(this.pro_width, Bar_type.OUTER, material, new Vector3(0, xBar_up_pos_x, 0.2875), AXIS.X, Direction.START, PLANE.XY));
        this.outer_Frame.add(new OuterBar(this.pro_width, Bar_type.OUTER, material, new Vector3(0, xBar_up_pos_x, -0.2875), AXIS.X, Direction.END, PLANE.XY));
        this.outer_Frame.add(new OuterBar(OUTER_Z_LENGTH, Bar_type.OUTER, material, new Vector3(0.4875, xBar_down_pos_x, 0), AXIS.Z, Direction.START));
        this.outer_Frame.add(new OuterBar(OUTER_Z_LENGTH, Bar_type.OUTER, material, new Vector3(-0.4875, xBar_down_pos_x, 0), AXIS.Z, Direction.END));
        this.outer_Frame.add(new OuterBar(OUTER_Z_LENGTH, Bar_type.OUTER, material, new Vector3(0.4875, xBar_up_pos_x, 0), AXIS.Z, Direction.START, PLANE.XZ));
        this.outer_Frame.add(new OuterBar(OUTER_Z_LENGTH, Bar_type.OUTER, material, new Vector3(-0.4875, xBar_up_pos_x, 0), AXIS.Z, Direction.END, PLANE.XZ));
        this.outer_Frame.add(new OuterBar(INIT_HEIGHT,  Bar_type.OUTER, material, new Vector3(0.4875, yBar_pos_y, 0.2875), AXIS.Y, Direction.start));
        this.outer_Frame.add(new OuterBar(INIT_HEIGHT, Bar_type.OUTER, material, new Vector3(0.4875, yBar_pos_y, -0.2875), AXIS.Y, Direction.START, PLANE.XY));
        this.outer_Frame.add(new OuterBar(INIT_HEIGHT, Bar_type.OUTER, material, new Vector3(-0.4875, yBar_pos_y, 0.2875), AXIS.Y, Direction.END));
        this.outer_Frame.add(new OuterBar(INIT_HEIGHT, Bar_type.OUTER, material, new Vector3(-0.4875, yBar_pos_y, -0.2875), AXIS.Y, Direction.END,PLANE.XY));

        this.scene.add(this.outer_Frame);
        for(var i = 0; i < this.outer_Frame.children.length; i++){
            this.inner_Group.push(this.outer_Frame.children[i]);
        }

        this.product.add(this.outer_Frame);
    }

    initArrow() {
        this.initArrowControl(new Arrow(AXIS.X));
        this.initArrowControl(new Arrow(AXIS.Y));
        this.initArrowControl(new Arrow(AXIS.Z));
    }

    initArrowControl(arrow) {
        const transformControl = new TransformControls(this.camera, this.renderer.domElement)
        switch(arrow.axis) {
            case AXIS.X:
                transformControl.showY = transformControl.showZ = false;
                this.arrow_x_control = transformControl;
                break;
            case AXIS.Y:
                transformControl.showX = transformControl.showZ = false;
                this.arrow_y_control = transformControl;
                break;
            case AXIS.Z:
                transformControl.showX = transformControl.showY = false;
                this.arrow_z_control = transformControl;
                break;
            default:
                break;
        }
        transformControl.attach(arrow);
        this.scene.add(arrow);
        this.scene.add(transformControl);

        transformControl.addEventListener("dragging-changed", this.OnArrowDraggingChanged);
        transformControl.addEventListener("objectChange", this.OnArrowObjectChanged);
    }

    render() {
        this.renderer.render(this.scene, this.camera);
    }

    resize() {

        this.camera.aspect = this.container.clientWidth/ this.container.clientHeight ;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize( this.container.clientWidth, this.container.clientHeight );
    }

    OnArrowDraggingChanged(event) {
        SceneController.instance().orbit.enabled = !event.value;
    }

    OnArrowObjectChanged() {
        this.object.OnArrowObjectChanged(); //this.object : attached object to TransformControl -> it is the Arrow object.
    }
    
    initPlane(){
        if(this.planeGroup === null){
            this.planeGroup = new THREE.Group();
        }else{
            for(let index = 0; index < this.planeGroup.children.length; index ++){
                this.planeGroup.remove(this.planeGroup.children[index]);
            }
        }
        let width, height, depth, pos_x, pos_y, pos_z, pos_y_up;
        for(let index = 0; index < this.inner_Group.length; index++){
            if(this.inner_Group[index].type === Bar_type.OUTER && this.inner_Group[index].axis === AXIS.X){
                width = this.inner_Group[index].userData.length * this.inner_Group[index].scale.y;
                pos_z = Math.abs(this.inner_Group[index].position.z);
                if(this.inner_Group[index].place === PLANE.XY){
                    pos_y_up = Math.abs(this.inner_Group[index].position.y);
                }
            }
            if(this.inner_Group[index].type === Bar_type.OUTER && this.inner_Group[index].axis === AXIS.Z){
                depth = this.inner_Group[index].userData.length * this.inner_Group[index].scale.y;
                pos_x = Math.abs(this.inner_Group[index].position.x);
            }
            if(this.inner_Group[index].type === Bar_type.OUTER && this.inner_Group[index].axis === AXIS.Y){
                height = this.inner_Group[index].userData.length * this.inner_Group[index].scale.y;
                pos_y = Math.abs(this.inner_Group[index].position.y);
            }
        }
        let plane_width = width - 2 * BAR_DEPTH;
        var init_y = BASEBAR_HEIGHT/2 + BAR_DEPTH/2;
        this.addPlane(new Plane(plane_width, depth, new Vector3(0, pos_y_up, 0), PLANE.XZ));
        this.addPlane(new Plane(plane_width, depth, new Vector3(0, init_y, 0), PLANE.XZ));
        this.addPlane(new Plane(plane_width, height, new Vector3(0, pos_y, pos_z), PLANE.XY));
        this.addPlane(new Plane(plane_width, height, new Vector3(0, pos_y, -pos_z), PLANE.XY));
        this.addPlane(new Plane(depth, height, new Vector3(pos_x, pos_y, 0), PLANE.YZ));
        this.addPlane(new Plane(depth, height, new Vector3(-pos_x, pos_y, 0), PLANE.YZ));
    
        this.scene.add(this.planeGroup);

    }

    initSpace(){
        if(this.boxSpace === null){
            this.boxSpace = new THREE.Group();
        }else{
            this.scene.remove(this.boxSpace);
        }
        
        let width = this.getSizeOuterFrame().width - 2 * BAR_DEPTH ;
        let height = this.getSizeOuterFrame().height -2 * BAR_DEPTH;
        let depth  = this.getSizeOuterFrame().depth - 2 * BAR_DEPTH;
        let box_geo = new THREE.BoxGeometry( width, height , depth );
        let material = new THREE.MeshBasicMaterial( { color: 0xf0fca0, visible: false } );
        let space = new THREE.Mesh( box_geo, material );
        this.boxSpace.add(space);
        this.boxSpace.position.set(0, INIT_Y_DOWN + height/2, 0);
        this.scene.add(this.boxSpace);
    }

    getSize(object){
        let measure = new THREE.Vector3();
        var boundingBox = new THREE.Box3().setFromObject(object)
        var size = boundingBox.getSize(measure); // Returns Vector3
        return {width : size.x, height : size.y, depth : size.z};
    }

    getSizeOuterFrame(){
        let width = this.getSize(this.outer_Frame).width - 2* DELTA_DOT;
        let height = this.getSize(this.outer_Frame).height -2* DELTA_DOT;
        let depth  = this.getSize(this.outer_Frame).depth - 2* DELTA_DOT;
        return {width : width, height : height,  depth : depth};
    }

    hideArrow(){
        this.arrow_x_control.showX = false;
        this.arrow_y_control.showY = false;
        this.arrow_z_control.showZ = false;
    }

    innerBar(axis, material){
        this.hideArrow();
        this.initPlane();
        this.createPlane();
        this.hideHover();
        let init_length ;
        switch(axis){
            case AXIS.X:
                init_length = BASEBAR_WIDTH - 2 * BAR_DEPTH;
                break;
            case AXIS.Y:
                init_length = INIT_HEIGHT;
                break;
            case AXIS.Z:
                init_length = OUTER_Z_LENGTH;
                break;
            default:
                break;
        }
        this.hoverbar =  new OuterBar(init_length, Bar_type.INNER, material, new THREE.Vector3(0, 0, 0), axis, Direction.START, PLANE.XZ, Bar_Group.SOLO );

        if(this.bar_set === "double")
            this.dou_bar = new OuterBar(init_length, Bar_type.INNER, material, new THREE.Vector3(0, 0, 0), axis, Direction.START, PLANE.XZ, Bar_Group.SOLO );
    }

    addComponent(){

        const material = new THREE.MeshStandardMaterial({
            color: 0x2b2d2e,
            emissive: 0x111111,
            metalness: 1,
            roughness: 0.55,
            opacity: 0.5,
            transparent: true
        });
        const inner_axis = document.querySelector('#inner_axis');
        inner_axis.addEventListener('click', ()=>{

            switch(inner_axis.value){
                case "inner_z_axis" :
                    this.inner_type = AXIS.Z;
                    break;
                case "inner_y_axis" :
                    this.inner_type = AXIS.Y;
                    break;
                case "inner_x_axis" :
                    this.inner_type = AXIS.X;
                    break;
                default:
                    break;
            }
            this.innerBar(this.inner_type, material)
            this.bar_set = "inner"

        })

        const double_axis = document.querySelector('#double_axis');
        double_axis.addEventListener('click', ()=>{

            switch(double_axis.value){
                case "double_z_axis" :
                    this.inner_type = AXIS.Z;
                    break;
                case "double_y_axis" :
                    this.inner_type = AXIS.Y;
                    break;
                case "double_x_axis" :
                    this.inner_type = AXIS.X;
                    break;
                default:
                    break;
            }
            this.innerBar(this.inner_type, material)
            this.bar_set = "double"
        })

        const iner_yz_set = document.querySelector('#Add_yz_set');
        
        iner_yz_set.addEventListener('click', ()=>{
            this.profile_set = PLANE.YZ;
            this.inner_Set(this.profile_set, material);
        }, false)

        let add_seperate_yz = document.querySelector('#Add_separation_yz');
        add_seperate_yz.addEventListener('click', ()=>{
            this.addSeperate(PLANE.YZ);
        })

        let add_seperate_xy = document.querySelector('#Add_separation_xy');
        add_seperate_xy.addEventListener('click', ()=>{
            this.addSeperate(PLANE.XY);
        })

        let add_seperate_xz = document.querySelector('#Add_separation_xz');
        add_seperate_xz.addEventListener('click', ()=>{
            this.addSeperate(PLANE.XZ);
        })

        let add_cover_yz = document.querySelector('#Add_cover_yz');
        add_cover_yz.addEventListener('click', ()=>{
            this.addCover(PLANE.YZ);
        })

        let add_cover_xy = document.querySelector('#Add_cover_xz');
        add_cover_xy.addEventListener('click', ()=>{
            this.addCover(PLANE.XZ);
        })

        let add_cover_xz = document.querySelector('#Add_cover_xy');
        add_cover_xz.addEventListener('click', ()=>{
            this.addCover(PLANE.XY);
        })

         const add_door  = document.querySelector('#Add_door');
         add_door.addEventListener('click', ()=>{
             this.addDoor();
        })

        const add_cablebox  = document.querySelector('#Add_cablebox');
        add_cablebox.addEventListener('click', ()=>{
            this.addCableBox();
       })

       const add_louver  = document.querySelector('#Add_louver');
       add_louver.addEventListener('click', ()=>{
            this.addLouver();
       })

       const add_compartment  = document.querySelector('#Add_Compartment');
       add_compartment.addEventListener('click', ()=>{
            this.addCompartment();
       })
    }

    addSeperate(place){
        this.hideHover();
        this.initSpace();
        let width, depth;
        switch(place){
            case PLANE.YZ:
                width = this.getSizeOuterFrame().depth ;
                depth = this.getSizeOuterFrame().height;
                break;
            case PLANE.XY:
                width = this.getSizeOuterFrame().width ;
                depth = this.getSizeOuterFrame().height;
                break;
            case PLANE.XZ:
                width = this.getSizeOuterFrame().width ;
                depth = this.getSizeOuterFrame().depth;
                break;
            default:
                break;
        }
        this.hoverseperate = new Separation(width, depth, new THREE.Vector3(0, 0, 0), place)
    }

    addCompartment(){
        this.initSpace();
        this.hideHover();
        this.hovercompartment = new Compartment(0.4, 0.2, 0.3, new Vector3(0, 0, 0 ));
    }

    addCover(place){
        this.hideHover();
        this.initSpace();
        let width, depth;
        switch(place){
            case PLANE.YZ:
                width = this.getSizeOuterFrame().depth ;
                depth = this.getSizeOuterFrame().height;
                break;
            case PLANE.XY:
                width = this.getSizeOuterFrame().width ;
                depth = this.getSizeOuterFrame().height;
                break;
            case PLANE.XZ:
                width = this.getSizeOuterFrame().width ;
                depth = this.getSizeOuterFrame().depth;
                break;
            default:
                break;
        }
        this.hovercover = new Cover(width, depth, new THREE.Vector3(0, 0, 0), place)
    }

    addDoor(){
        this.initSpace();
        if(this.doors.children.length !== 0)
            return;
        this.hideHover();
        let width = this.getSizeOuterFrame().width;
        let height = this.getSizeOuterFrame().height;
        this.hoverdoor = new Door(width, height, new Vector3(0, 0,0), PLANE.XY); 
    }

    addCableBox(){
        this.hideHover();
        this.initSpace();
        this.hovercablebox = new CableBox(new Vector3(0, 0, 0));
    }

    addLouver(){
        if(this.louvers.children.length !==0)
            return;
        this.hideHover();
        this.hideArrow();
        this.initSpace();
        let width = this.getSizeOuterFrame().width;
        let height = this.getSizeOuterFrame().height;
        this.hoverlouver = new Louver(width, height, new Vector3(0,0,0), PLANE.XY); 
    }

    inner_Set(set_type, material){
        this.initSpace();
        this.scene.remove(this.hoverbar);
        this.hoverbar = null;
        this.hideAllPlane();
        var init_width, init_depth;
        if(set_type === PLANE.YZ) {
            init_width = this.getSizeOuterFrame().depth - 2*BAR_DEPTH;
            init_depth = this.getSizeOuterFrame().height  - 2 * BAR_DEPTH;
        }
        this.hoverset =  new ProfileSet(init_width, init_depth,  material, new THREE.Vector3(0, 0, 0), set_type);
    }

    getPlane(type){
        var plane_type =[];
        for(var i = 0; i < this.planeGroup.children.length; i++){
            if(type === AXIS.Z){
                if(this.planeGroup.children[i].place === PLANE.XZ || this.planeGroup.children[i].place === PLANE.YZ){
                    plane_type.push(this.planeGroup.children[i]);
                }
            }
            if(type === AXIS.Y){
                if(this.planeGroup.children[i].place === PLANE.XY || this.planeGroup.children[i].place === PLANE.YZ){
                    plane_type.push(this.planeGroup.children[i]);
                }
            } 
            if(type === AXIS.X){
                if(this.planeGroup.children[i].place === PLANE.XZ || this.planeGroup.children[i].place === PLANE.XY){
                    plane_type.push(this.planeGroup.children[i]);
                }
            }
        }
        return plane_type;
    }


    onPointerMove(event){
        if(this.pointer === null)
            this.pointer = new THREE.Vector2();
        if(this.raycaster === null)
            this.raycaster =  new THREE.Raycaster();

        this.pointer.set( ( (event.clientX ) / this.container.clientWidth ) * 2 - 1, - ( (event.clientY -60) / this.container.clientHeight ) * 2 + 1 );
        this.raycaster.setFromCamera( this.pointer, this.camera ); 
        if(this.hoverbar !== null){
            var plane_type = this.getPlane(this.inner_type);
            const intersects = this.raycaster.intersectObjects( plane_type,  false );
            if ( intersects.length > 0 ) {
                const intersect = intersects[ 0 ];
                this.hideAllPlane();
                var bars_group = this.getBarGroup(intersect.object.place, this.inner_type, BAR_STATUS.HOVER, intersect.object.position, intersect.point);
                this.getLength(bars_group, this.inner_type, intersect.point );
                this.hoverbar.position.copy( intersect.point );
                this.hoverbar.setBar(this.inner_type, this.bar_length,  this.bar_center, intersect.object.place, this.bar_set, Direction.START);
                this.scene.add(this.hoverbar);

                if(this.bar_set === "double"){
                    this.dou_bar.position.copy( intersect.point );
                    this.dou_bar.setBar(this.inner_type, this.bar_length,  this.bar_center, intersect.object.place, this.bar_set, Direction.END);
                    this.scene.add(this.dou_bar);
                }

            }
        }else{
            if(this.hoverset !== null){
                const intersects = this.raycaster.intersectObjects( this.boxSpace.children,  false );
                if ( intersects.length > 0 ) {
                    this.hoverset.position.copy( intersects[ 0 ].point );
                    if(this.profile_set === PLANE.YZ){
                        this.hoverset.position.z = 0;
                        this.hoverset.position.x = Math.floor(intersects[ 0 ].point.x *10)/10;
                        this.hoverset.position.y = (this.getSizeOuterFrame().height - 2 * BAR_DEPTH)/2 + INIT_Y_DOWN;
                    }
                    this.scene.add(this.hoverset);
                }
            }else{
                const intersects = this.raycaster.intersectObjects( this.inner_Group,  false );
                if ( intersects.length > 0 ) {
                    if(intersects[ 0 ].object.type !== Bar_type.OUTER){
                        this.innerhovered = intersects[ 0 ].object;
                        this.innerhovered.checkMovable();
                        if(this.innerhovered.getMovable()) this.innerhovered.material.color.setHex(0x2b2d2e);
                    }
                }else{
                    if ( this.innerhovered ) this.innerhovered.material.color.setHex(0xd5d2d4);
                    this.innerhovered = null;
                }
            }
        }
        if(this.innerselected !== null && this.isPointerDown){
            for(let index = 0 ; index < this.inner_Group.length; index ++){
                if(this.inner_Group[index].type === Bar_type.INNER && this.inner_Group[index] !== this.innerselected){
                   this.inner_Group[index].material.color.setHex(0xd5d2d4);
                }
            }
            plane_type = this.getPlane(this.innerselected.axis);
            const intersects = this.raycaster.intersectObjects( plane_type,  false );
            if ( intersects.length > 0 ) {
                const intersect = intersects[ 0 ];
                this.innerselected.checkMovable();
                if(this.innerselected.getMovable()){
                    console.log("can move");
                    this.innerselected.position.copy( this.selected_center);
                    if(this.innerselected.place !==PLANE.YZ && this.innerselected.axis !== AXIS.X){
                      //  this.innerselected.position.x = intersect.point.x;
                        this.innerselected.position.x = Math.floor(intersect.point.x*10)/10;
                        this.innerselected.setX_LimitPos();
                    }
                    if(this.innerselected.place !==PLANE.XY && this.innerselected.axis !== AXIS.Z){
                        this.innerselected.position.z = Math.floor(intersect.point.z*10)/10;
                        this.innerselected.setZ_LimitPos();
                    }
                    if(this.innerselected.place !==PLANE.XZ && this.innerselected.axis !== AXIS.Y){
                        this.innerselected.position.y = Math.floor((intersect.point.y - INIT_Y_DOWN)*10)/10 +INIT_Y_DOWN;
                        this.innerselected.setY_LimitPos();
                    }

                }else{
                    console.log("can't move");
                }

                this.showbarSize(this.innerselected);
            }
        }

        if(this.hoverseperate !== null){
            let intersects = this.raycaster.intersectObjects( this.boxSpace.children,  false );
            if ( intersects.length > 0 ) {
                this.hoverseperate.position.copy( intersects[ 0 ].point );
                if(this.hoverseperate.place === PLANE.YZ){
                    this.hoverseperate.position.z = 0;
                    this.hoverseperate.position.x = Math.floor(intersects[0].point.x *10)/10;
                    this.hoverseperate.position.y = (this.getSizeOuterFrame().height - 2 * BAR_DEPTH)/2 + BASEBAR_HEIGHT/2 + BAR_DEPTH;
                    this.scene.add(this.hoverseperate);
                    this.hoverSize(AXIS.Y, this.hoverseperate.place, this.hoverseperate);
                    this.hoverSize(AXIS.Z, this.hoverseperate.place, this.hoverseperate); 
                }
                if(this.hoverseperate.place === PLANE.XY){
                    this.hoverseperate.position.x = 0;
                    this.hoverseperate.position.y = (this.getSizeOuterFrame().height - 2 * BAR_DEPTH)/2 + BASEBAR_HEIGHT/2 + BAR_DEPTH;
                    this.scene.add(this.hoverseperate);
                    this.hoverSize(AXIS.Y, this.hoverseperate.place, this.hoverseperate);
                    this.hoverSize(AXIS.X, this.hoverseperate.place, this.hoverseperate); 
                }
                if(this.hoverseperate.place === PLANE.XZ){
                    this.hoverseperate.position.x = 0;
                    this.hoverseperate.position.z = 0;
                    this.hoverseperate.position.y =  intersects[0].point.y;
                    this.scene.add(this.hoverseperate);
                    this.hoverSize(AXIS.Z, this.hoverseperate.place, this.hoverseperate);
                    this.hoverSize(AXIS.X, this.hoverseperate.place, this.hoverseperate); 
                }
            }
        }

        if(this.hovercover !== null){
            let intersects = this.raycaster.intersectObjects( this.boxSpace.children,  false );
            if ( intersects.length > 0 ) {
                this.hovercover.position.copy( intersects[ 0 ].point );
                if(this.hovercover.place === PLANE.YZ){
                    this.hovercover.position.z = 0;
                    this.hovercover.position.y = (this.getSizeOuterFrame().height - 2 * BAR_DEPTH)/2 + BASEBAR_HEIGHT/2 + BAR_DEPTH;
                    this.scene.add(this.hovercover);
                    this.hoverSize(AXIS.Y, this.hovercover.place, this.hovercover);
                    this.hoverSize(AXIS.Z, this.hovercover.place, this.hovercover); 
                    if(intersects[0].point.x >=0)
                        this.hovercover.position.x = this.getSizeOuterFrame().width/2 + COVER_DEPTH;
                    else    
                        this.hovercover.position.x = -(this.getSizeOuterFrame().width/2 + COVER_DEPTH);
                }
                if(this.hovercover.place === PLANE.XY){
                    this.hovercover.position.x = 0;
                    this.hovercover.position.y = (this.getSizeOuterFrame().height - 2 * BAR_DEPTH)/2 + BASEBAR_HEIGHT/2 + BAR_DEPTH;
                    this.scene.add(this.hovercover);
                    this.hoverSize(AXIS.Y, this.hovercover.place, this.hovercover);
                    this.hoverSize(AXIS.X, this.hovercover.place, this.hovercover); 
                    this.hovercover.position.z = -(this.getSizeOuterFrame().depth/2 + COVER_DEPTH);
                }
                if(this.hovercover.place === PLANE.XZ){
                    this.hovercover.position.x = 0;
                    this.hovercover.position.z = 0;
                    this.hovercover.position.y =  intersects[0].point.y;
                    this.scene.add(this.hovercover);
                    this.hoverSize(AXIS.Z, this.hovercover.place, this.hovercover);
                    this.hoverSize(AXIS.X, this.hovercover.place, this.hovercover); 
                    this.hovercover.position.y = this.getSizeOuterFrame().height + BASEBAR_HEIGHT/2 + COVER_DEPTH;
                }
            }
        }

        if(this.hoverdoor !== null){
            let intersects = this.raycaster.intersectObjects( this.boxSpace.children,  false );
            if ( intersects.length > 0 ) {
                this.hoverdoor.position.z = intersects[0].point.z;
                this.hoverdoor.position.y = (this.getSizeOuterFrame().height - 2 * BAR_DEPTH)/2 + BASEBAR_HEIGHT/2 + BAR_DEPTH;
                this.scene.add(this.hoverdoor);
                this.hoverSize(AXIS.Y, PLANE.XY, this.hoverdoor);
                this.hoverSize(AXIS.X, PLANE.XY, this.hoverdoor); 
                this.hoverdoor.position.z = this.getSizeOuterFrame().depth/2 + COVER_DEPTH;
            }
        }

        if(this.hoverlouver !== null){
            let intersects = this.raycaster.intersectObjects( this.boxSpace.children,  false );
            if ( intersects.length > 0 ) {

                this.hoverlouver.position.z = intersects[0].point.z;
                this.hoverlouver.position.y = (this.getSizeOuterFrame().height - 2 * BAR_DEPTH)/2 + BASEBAR_HEIGHT/2 + BAR_DEPTH;
                this.scene.add(this.hoverlouver);
                this.hoverSize(AXIS.Y, PLANE.XY, this.hoverlouver);
                this.hoverSize(AXIS.X, PLANE.XY, this.hoverlouver); 
                this.hoverlouver.position.z = this.getSizeOuterFrame().depth/2 + COVER_DEPTH;
            }
        }

        if(this.hovercablebox !== null){
            const intersects = this.raycaster.intersectObjects( this.boxSpace.children,  false );
            if ( intersects.length > 0 ) {
                this.hovercablebox.position.copy( intersects[ 0 ].point );
                this.hovercablebox.position.z = 0;
                this.scene.add(this.hovercablebox);
            }
        }
        if(this.hovercompartment !== null){
            const intersects = this.raycaster.intersectObjects( this.boxSpace.children,  false );
            if ( intersects.length > 0 ) {
                this.hovercompartment.position.copy( intersects[ 0 ].point );
                this.scene.add(this.hovercompartment);
            }
        }
        this.render();
    }

    hoverSize(axis, place,element){
        if(this.planeGroup=== null)
            return;
        let plane_type = this.getPlane(axis);
        let intersects_plane = this.raycaster.intersectObjects( plane_type,  false );
        if ( intersects_plane.length > 0 ) {
            let intersect = intersects_plane[ 0 ];
            this.hideAllPlane();
            let bars_group = this.getBarGroup(intersect.object.place, axis, BAR_STATUS.HOVER, intersect.object.position, intersect.point);
            this.getLength(bars_group, axis, intersect.point );
            switch(axis){
                case AXIS.X:
                    element.scale.x = this.bar_length/ element.userData.width;
                    element.position.x = this.bar_center;
                    break;
                case AXIS.Z:
                    if(place === PLANE.YZ){
                        element.scale.x = this.bar_length/ element.userData.width;
                        element.position.z = this.bar_center;
                    }else if(place === PLANE.XZ){
                        element.scale.y = this.bar_length/ element.userData.depth;
                        element.position.z = this.bar_center;
                    }
                    break;
                case AXIS.Y:
                    element.scale.y = this.bar_length/ element.userData.depth;
                    element.position.y = this.bar_center;
                    element.position.x = intersect.point.x;
                    break;
                default:
                    break;
            }  
        }
    }

    onPointerDown(event){
        this.isPointerDown = true;
        this.pointer.set( ( (event.clientX) / this.container.clientWidth ) * 2 - 1, - ( (event.clientY -60) / this.container.clientHeight ) * 2 + 1 );
        this.raycaster.setFromCamera( this.pointer, this.camera );
        let material = new THREE.MeshStandardMaterial({
            color: 0xd5d2d4,
            emissive: 0x111111,
            metalness: 1,
            roughness: 0.55,
            transparent: true
        });

        if(this.hoverbar !== null){
            var plane_type = this.getPlane(this.inner_type);
            const intersects = this.raycaster.intersectObjects( plane_type, false );
            if ( intersects.length > 0 ) {
                const intersect = intersects[ 0 ];
                var innerbar = new OuterBar(this.bar_length, Bar_type.INNER, material, new Vector3(0, 0, 0), this.inner_type,  Direction.MIDDLE, intersect.object.place, Bar_Group.SOLO, this.scene);
                innerbar.position.set(Number((intersect.point.x).toFixed(4)), Number((intersect.point.y).toFixed(4)), Number((intersect.point.z).toFixed(4)));
                innerbar.setBar(this.inner_type, this.bar_length,  this.bar_center,  intersect.object.place, this.bar_set, Direction.START);
                this.scene.add(innerbar);
                this.inner_Group.push(innerbar);
                innerbar.setParentBar(this.start_bar, this.end_bar);
                this.start_bar.addConnectchild(innerbar);
                this.end_bar.addConnectchild(innerbar);

                if(this.bar_set === "double"){
                    var sec_bar = new OuterBar(this.bar_length, Bar_type.INNER, material, new Vector3(0, 0, 0), this.inner_type,  Direction.MIDDLE, intersect.object.place, Bar_Group.SOLO, this.scene);
                    sec_bar.position.set(Number((intersect.point.x).toFixed(4)), Number((intersect.point.y).toFixed(4)), Number((intersect.point.z).toFixed(4)));
                    sec_bar.setBar(this.inner_type, this.bar_length,  this.bar_center,  intersect.object.place, this.bar_set, Direction.END);
                    this.scene.add(sec_bar);
                    this.inner_Group.push(sec_bar);
                    console.log(innerbar.position, sec_bar.position)
                }

            }
        }else{
            if(this.hoverset !== null){
                const intersects = this.raycaster.intersectObjects( this.boxSpace.children, false );
                if(intersects.length > 0){

                    var position = new Vector3();
                    if(this.profile_set === PLANE.YZ){
                        position.copy( this.hoverset.position);
                    }
                    let profile = new ProfileSet(this.hoverset.userData.width, this.hoverset.userData.depth, material, position, PLANE.YZ);
                    this.scene.add(profile);
                    for(var i = 0 ; i< profile.children.length; i ++){
                        this.inner_Group.push(profile.children[i]);
                    }
                }
            }else{
                this.innerselected = this.innerhovered;
                
                if(this.innerselected !== null && this.hoverseperate === null){
                    ///////////////////////
                    let intersects = this.raycaster.intersectObjects( this.inner_Group,  false );
                    if ( intersects.length > 0 ) {
                        let bars_group = this.getBarGroup(this.innerselected.place, this.innerselected.axis, BAR_STATUS.SELECT, this.innerselected.position, intersects[0].point);
                        this.getRange(bars_group, this.innerselected);
                        this.orbit.enabled = false;
                        this.selected_center.set(this.innerselected.position.x, this.innerselected.position.y, this.innerselected.position.z);
                        this.innerselected.setRangeBar(this.from_bar, this.to_bar);
                        this.showbarSize(this.innerhovered);
                        if(this.gui !== null)
                            SceneController.instance().gui.destroy();
                        this.gui = new GUI();
                        // this. innerselected.gui = this.gui;
    
                        this.innerselected.delete = function() {
                            this.removeUserdata();
                            SceneController.instance().gui.destroy();
                        };
                        this.innerselected.finish = function() {
                            SceneController.instance().gui.destroy();
                        };
                        this.gui.add(this.innerselected,'delete').name('Delete');
                        this.gui.add(this.innerselected,'finish').name('Finish');
                    }
                    //////////////////////
                }else{
                    this.orbit.enabled = true;
                }
            }
        }
        if(this.hoverseperate !== null){
            const intersects = this.raycaster.intersectObjects( this.boxSpace.children,  false );
            if ( intersects.length > 0 ) {
                let width = this.hoverseperate.userData.width * this.hoverseperate.scale.x;
                let height = this.hoverseperate.userData.depth * this.hoverseperate.scale.y;
                let position = new Vector3();
                position.set(this.hoverseperate.position.x, this.hoverseperate.position.y, this.hoverseperate.position.z);
                let seperation  = new Separation(width, height, position, this.hoverseperate.place)
                this.product.add( this.separation_group);
                this.separation_group.add(seperation);
            }
        }

        if(this.hovercover !== null){
            const intersects = this.raycaster.intersectObjects( this.boxSpace.children,  false );
            if ( intersects.length > 0 ) {
                let width = this.hovercover.userData.width * this.hovercover.scale.x;
                let height = this.hovercover.userData.depth * this.hovercover.scale.y;
                let position = new Vector3();
                position.set(this.hovercover.position.x, this.hovercover.position.y, this.hovercover.position.z);
                let cover  = new Cover(width, height, position, this.hovercover.place)
                this.product.add( this.covers);
                this.covers.add(cover);
            }
        }
        
        if(this.hoverdoor !== null){
            const intersects = this.raycaster.intersectObjects( this.boxSpace.children,  false );
            if ( intersects.length > 0 ) {
                let width = this.hoverdoor.userData.width * this.hoverdoor.scale.x;
                let height = this.hoverdoor.userData.depth * this.hoverdoor.scale.y;
                let position = new Vector3();
                position.set(this.hoverdoor.position.x, this.hoverdoor.position.y, this.hoverdoor.position.z);
                let door  = new Door(width, height, position, this.hoverdoor.place)
                this.product.add( this.doors);
                this.doors.add(door);
            }
        }

        if(this.hoverlouver !== null){
            const intersects = this.raycaster.intersectObjects( this.boxSpace.children,  false );
            if ( intersects.length > 0 ) {
                let width = this.hoverlouver.userData.width * this.hoverlouver.scale.x;
                let height = this.hoverlouver.userData.depth * this.hoverlouver.scale.y;
                let position = new Vector3();
                position.set(this.hoverlouver.position.x, this.hoverlouver.position.y, this.hoverlouver.position.z);
                let louver  = new Louver(width, height, position, this.hoverlouver.place)
                this.product.add( this.louvers);
                this.louvers.add(louver);
            }
        }

        if(this.hovercablebox !== null){
            const intersects = this.raycaster.intersectObjects( this.boxSpace.children,  false );
            if ( intersects.length > 0 ) {
                const cablebox  = new CableBox(new Vector3(0, 0, 0));
                cablebox.position.set(this.hovercablebox.position.x, this.hovercablebox.position.y, this.hovercablebox.position.z);
                this.product.add(cablebox);
            }
        }
        if(this.hovercompartment!== null){
            const intersects = this.raycaster.intersectObjects( this.boxSpace.children,  false );
            if ( intersects.length > 0 ) {
                const compartment  = new Compartment(0.4, 0.2, 0.3, new Vector3(0, 0, 0 ));
                compartment.position.set(this.hovercompartment.position.x, this.hovercompartment.position.y, this.hovercompartment.position.z);
                compartment.position.z = 0;
                this.product.add(compartment);
            }
        }

        const intersects = this.raycaster.intersectObjects( this.doors.children, false );
        if ( intersects.length > 0 ) {
            if(this.gui !== null)
                SceneController.instance().gui.destroy();
            this.gui = new GUI();
            const params = {
				ShowDoor: true,
			};
            this.gui.add( params, 'ShowDoor' ).listen().onChange(function(value){
                intersects[ 0 ].object.material.visible = value;
            })
            this.gui.open(); 
        }
        

        let intersects_sep = this.raycaster.intersectObjects( this.separation_group.children, false );
        if ( intersects_sep.length > 0 ) {
            if(this.gui !== null)
                SceneController.instance().gui.destroy();
            this.gui = new GUI();

            intersects_sep[ 0 ].object.delete = function() {
                this.parent.remove(this);
                SceneController.instance().gui.destroy();
            };
            this.gui.add(intersects_sep[ 0 ].object,'delete').name('Delete');
            this.gui.open(); 
        }

        let intersects_cover = this.raycaster.intersectObjects( this.covers.children, false );
        if ( intersects_cover.length > 0 ) {
            if(this.gui !== null)
                SceneController.instance().gui.destroy();
            this.gui = new GUI();

            intersects_cover[ 0 ].object.delete = function() {
                this.parent.remove(this);
                SceneController.instance().gui.destroy();
            };
            this.gui.add(intersects_cover[ 0 ].object,'delete').name('Delete');
            this.gui.open(); 
        }

        let intersects_louver = this.raycaster.intersectObjects( this.louvers.children, false );
        if ( intersects_louver.length > 0 ) {
            if(this.gui !== null)
                SceneController.instance().gui.destroy();
            this.gui = new GUI();

            intersects_louver[ 0 ].object.delete = function() {
                this.parent.remove(this);
                SceneController.instance().gui.destroy();
            };
            this.gui.add(intersects_louver[ 0 ].object,'delete').name('Delete');
            this.gui.open(); 
        }

        this.render();
    }

    delelteGUI(){
        this.scene.remove(this.innerselected);
        this.gui.destroy();
    }

    onPointerUp(event){
        this.isPointerDown = false;
    }

    checkSideBar(point, start, end){
        if(point >=start && point <= end)
            return true;
        else   
            return false;
    }

    getBarGroup(place,inner_type, status,  position, point){
        let X_bars = [];
        let Y_bars = [];
        let Z_bars = [];

        let parent_bars = [];
        let friend_bars = [];
        let bar_group = [];
        for(var i = 0; i < this.inner_Group.length; i++){
            switch(place){
                case PLANE.XZ:
                    if(this.inner_Group[i].axis === AXIS.X && this.inner_Group[i].position.y === position.y){
                        let bar = this.inner_Group[i];
                        if(this.checkSideBar(point.x, bar.getSidePos().start, bar.getSidePos().end ))
                            X_bars.push(this.inner_Group[i]);
                    }
                    if(this.inner_Group[i].axis === AXIS.Z && this.inner_Group[i].position.y === position.y){
                        let bar = this.inner_Group[i];
                        if(this.checkSideBar(point.z, bar.getSidePos().start, bar.getSidePos().end ))
                            Z_bars.push(this.inner_Group[i]);
                    }
                    if(status === BAR_STATUS.HOVER){
                        if(inner_type === AXIS.X) parent_bars = Z_bars;
                        if(inner_type === AXIS.Z) parent_bars = X_bars;
                    }else{
                        if(inner_type === AXIS.X) friend_bars = X_bars;
                        if(inner_type === AXIS.Z) friend_bars = Z_bars;
                    }
                    break;
                case PLANE.YZ:
                    if(this.inner_Group[i].axis === AXIS.Y && this.inner_Group[i].position.x === position.x){
                        let bar = this.inner_Group[i];
                        if(this.checkSideBar(point.y, bar.getSidePos().start, bar.getSidePos().end ))
                            Y_bars.push(this.inner_Group[i]);
                    }
                    if(this.inner_Group[i].axis === AXIS.Z && this.inner_Group[i].position.x === position.x){
                        let bar = this.inner_Group[i];
                        if(this.checkSideBar(point.z, bar.getSidePos().start, bar.getSidePos().end ))
                            Z_bars.push(this.inner_Group[i]);
                    }
                    if(status === BAR_STATUS.HOVER){
                        if(inner_type === AXIS.Y) parent_bars = Z_bars;
                        if(inner_type === AXIS.Z) parent_bars = Y_bars;
                    }else{
                        if(inner_type === AXIS.Y) friend_bars = Y_bars;
                        if(inner_type === AXIS.Z) friend_bars = Z_bars;
                    }
                    break;
                case PLANE.XY:
                    if(this.inner_Group[i].axis === AXIS.Y  && this.inner_Group[i].position.z === position.z){
                        let bar = this.inner_Group[i];
                        if(this.checkSideBar(point.y, bar.getSidePos().start, bar.getSidePos().end ))
                            Y_bars.push(this.inner_Group[i]);
                    }
                    if(this.inner_Group[i].axis === AXIS.X && this.inner_Group[i].position.z === position.z){
                        let bar = this.inner_Group[i];
                        if(this.checkSideBar(point.x, bar.getSidePos().start, bar.getSidePos().end ))
                            X_bars.push(this.inner_Group[i]);
                    }
                    
                    if(status === BAR_STATUS.HOVER){
                        if(inner_type === AXIS.Y) parent_bars = X_bars;
                        if(inner_type === AXIS.X) parent_bars = Y_bars;
                    }else{  
                        if(inner_type === AXIS.Y) 
                             friend_bars = Y_bars;
                        else if(inner_type === AXIS.X) friend_bars = X_bars;
                    }
                    break;
                default:
                    break;
            }
        }
        if(status === BAR_STATUS.HOVER){
            bar_group = parent_bars;
        }else{
           bar_group = friend_bars;
        }
        return bar_group;
    }

    getLength(bars, inner_type, point){
        let s_index, e_index;
        let x_pos_group = [];
        let y_pos_group = [];
        let z_pos_group = [];

        for(let index = 0 ; index < bars.length; index ++){
            x_pos_group.push(bars[index].position.x);
            y_pos_group.push(bars[index].position.y);
            z_pos_group.push(bars[index].position.z);
        }

        if(inner_type === AXIS.Z){
            s_index = this.getNearIndex(z_pos_group, point.z).start;
            e_index = this.getNearIndex(z_pos_group, point.z).end;
            this.bar_center = (z_pos_group[e_index] + z_pos_group[s_index])/2;
           this.bar_length = Math.abs(z_pos_group[e_index] - z_pos_group[s_index]) - BAR_DEPTH;
        }
        if(inner_type === AXIS.X){
            s_index = this.getNearIndex(x_pos_group, point.x).start;
            e_index = this.getNearIndex(x_pos_group, point.x).end;
            this.bar_center = (x_pos_group[e_index]+ x_pos_group[s_index])/2;
            this.bar_length = Math.abs(x_pos_group[e_index]- x_pos_group[s_index]) - BAR_DEPTH;
        }

        if(inner_type === AXIS.Y){
            s_index = this.getNearIndex(y_pos_group, point.y).start;
            e_index = this.getNearIndex(y_pos_group, point.y).end;
            this.bar_center = (y_pos_group[e_index] + y_pos_group[s_index])/2;
            this.bar_length = Math.abs(y_pos_group[e_index] - y_pos_group[s_index]) - BAR_DEPTH;
        }

        this.start_bar = bars[s_index];
        this.end_bar = bars[e_index];
    }

    getRange(bars, selectedbar){

        let s_index, e_index;
        let x_pos_group = [];
        let y_pos_group = [];
        let z_pos_group = [];
        let inner_type = selectedbar.axis;
        let point = selectedbar.position;
        let place = selectedbar.place;
        for(let index = 0 ; index < bars.length; index ++){
            if(selectedbar === bars[index])
                bars.splice(index, 1);
        }

        for(let index = 0 ; index < bars.length; index ++){
            x_pos_group.push(bars[index].position.x);
            y_pos_group.push(bars[index].position.y);
            z_pos_group.push(bars[index].position.z);
        }

        
        if(inner_type === AXIS.Z){
            
            if(place === PLANE.XZ){
                s_index = this.getNearIndex(x_pos_group, point.x).start;
                e_index = this.getNearIndex(x_pos_group, point.x).end;
            }else if(place === PLANE.YZ){
                s_index = this.getNearIndex(y_pos_group, point.y).start;
                e_index = this.getNearIndex(y_pos_group, point.y).end;
            }
        }
        if(inner_type === AXIS.X){
            if(place === PLANE.XZ){
                s_index = this.getNearIndex(z_pos_group, point.z).start;
                e_index = this.getNearIndex(z_pos_group, point.z).end;
            }else if(place === PLANE.XY){
                s_index = this.getNearIndex(y_pos_group, point.y).start;
                e_index = this.getNearIndex(y_pos_group, point.y).end;
            }
        }

        if(inner_type === AXIS.Y){
            if(place === PLANE.XY){
                s_index = this.getNearIndex(x_pos_group, point.x).start;
                e_index = this.getNearIndex(x_pos_group, point.x).end;
            }else if(place === PLANE.YZ){
                s_index = this.getNearIndex(z_pos_group, point.z).start;
                e_index = this.getNearIndex(z_pos_group, point.z).end;
            }
        }
        
        this.from_bar = bars[s_index];
        this.to_bar = bars[e_index];
    }

   getNearIndex(group, point){
        let min_dis;
        let distance;
        let max_value= 5;
        min_dis = max_value;
        let s_index, e_index;
        for(let index = 0; index < group.length; index ++){
            distance = Math.abs(group[index] - point);
            if(distance <= min_dis){
                min_dis = distance;
                s_index = index;
            }
        }
    
        min_dis = max_value;
        for(let index = 0; index < group.length; index ++){
            if(index  !== s_index){
                if(group[s_index] > point){
                    if(group[index] < point){
                        distance = Math.abs(group[index] - point);
                        if(distance <= min_dis){
                            min_dis = distance;
                            e_index = index;
                        }
                    }
                }else{
                    if(group[index] > point){
                        distance = Math.abs(group[index] - point);
                        if(distance <= min_dis){
                            min_dis = distance;
                            e_index = index;
                        }
                    }
                }
            }
        }

        if(group[s_index] > group[e_index]) {
            var temp = e_index;
            e_index = s_index;
            s_index = temp;
        }
    
        return {start : s_index,  end: e_index};
    }

    hideHover(){
        this.scene.remove(this.hoverbar);
        this.scene.remove(this.dou_bar);
        this.scene.remove(this.hoverset);
        this.scene.remove(this.hoverseperate);
        this.scene.remove(this.hovercablebox);
        this.scene.remove(this.hovercompartment);
        this.scene.remove(this.hoverdoor);
        this.scene.remove(this.hoverlouver);
        this.scene.remove(this.hovercover);
        this.hoverbar = null;

        this.hoverset = null;
        this.hoverseperate = null;
        this.hovercablebox = null;
        this.hovercompartment = null;
        this.innerselected = null;
        this.isPointerDown = false;
        this.hoverdoor = null;
        this.hoverlouver = null;
        this.hovercover = null;
        this.hideAllPlane();
    }

    onDocumentKeyDown(event){
        switch ( event.keyCode ) {
            case 27: 
                this.hideHover();
                break;
            default:
                break;
        }
    }

    hideAllPlane(){
        if(this.planeGroup !== null){
            for(var i = 0 ; i < this.planeGroup.children.length; i++){
                this.planeGroup.children[i].material.visible = false;
            }
        }
    }

    addPlane(plane){
        let isInclude = false;
        if(this.planeGroup !== null){
            for(let index = 0; index < this.planeGroup.children.length; index ++){
                if(this.planeGroup.children[index] === plane)
                    isInclude = true;
            }
            if(!isInclude) this.planeGroup.add(plane);
        }
    }

    createPlane(){        
        var X_bars, Y_bars, Z_bars;
        var plane_depth, plane_width, pos_x, pos_y, pos_z;
        X_bars = [];
        Y_bars = [];
        Z_bars = [];
        for(var i = 0; i < this.inner_Group.length; i++){
            if(this.inner_Group[i].type === Bar_type.INNER){
                if(this.inner_Group[i].axis === AXIS.X) X_bars.push(this.inner_Group[i]);
                if(this.inner_Group[i].axis === AXIS.Y) Y_bars.push(this.inner_Group[i]);
                if(this.inner_Group[i].axis === AXIS.Z) Z_bars.push(this.inner_Group[i]);
            }
        }

        for(var i =0; i < X_bars.length; i++){
            for(var j = i+1; j < X_bars.length; j++){
                if(X_bars[i].position.y === X_bars[j].position.y && X_bars[i].position.x === X_bars[j].position.x){
                    if(X_bars[i].userData.length !== X_bars[j].userData.length)
                        return;
                     plane_width = X_bars[i].userData.length - 2 * BAR_DEPTH;
                     plane_depth = Math.abs(X_bars[i].position.z - X_bars[j].position.z) -BAR_DEPTH;
                     pos_z = (X_bars[i].position.z + X_bars[j].position.z)/2;
                     pos_x = X_bars[i].position.x;
                     pos_y = X_bars[i].position.y;
                    this.addPlane(new Plane(plane_width, plane_depth, new Vector3(pos_x, pos_y, pos_z), PLANE.XZ));
                }
                if(X_bars[i].position.z === X_bars[j].position.z && X_bars[i].position.x === X_bars[j].position.x){
                    if(X_bars[i].userData.length !== X_bars[j].userData.length)
                        return;
                     plane_width = X_bars[i].userData.length - 2 * BAR_DEPTH;
                     plane_depth = Math.abs(X_bars[i].position.y - X_bars[j].position.y)  -BAR_DEPTH;
                     pos_y = (X_bars[i].position.y + X_bars[j].position.y)/2;
                     pos_x = X_bars[i].position.x;
                     pos_z = X_bars[i].position.z;
                    this.addPlane(new Plane(plane_width, plane_depth, new Vector3(pos_x, pos_y, pos_z), PLANE.XY)); 
                }
            }
        }

        for(var i =0; i < Z_bars.length; i++){
            for(var j = i+1; j < Z_bars.length; j++){
                if(Z_bars[i].position.y === Z_bars[j].position.y && Z_bars[i].position.z === Z_bars[j].position.z){
                    if(Z_bars[i].userData.length !== Z_bars[j].userData.length)
                        return;
                    plane_depth = Z_bars[i].userData.length;
                     plane_width = Math.abs(Z_bars[i].position.x - Z_bars[j].position.x) - BAR_DEPTH;
                     pos_x = (Z_bars[i].position.x + Z_bars[j].position.x)/2;
                     pos_y = Z_bars[i].position.y;
                     pos_z = Z_bars[i].position.z;
                    this.addPlane(new Plane(plane_width, plane_depth, new Vector3(pos_x, pos_y, pos_z), PLANE.XZ));
                }
                if(Z_bars[i].position.z === Z_bars[j].position.z && Z_bars[i].position.x === Z_bars[j].position.x){
                    if(Z_bars[i].userData.length !== Z_bars[j].userData.length)
                        return;
                     plane_width = Z_bars[i].userData.length;
                     plane_depth = Math.abs(Z_bars[i].position.y - Z_bars[j].position.y)  - BAR_DEPTH;
                     pos_y = (Z_bars[i].position.y + Z_bars[j].position.y)/2;
                     pos_x = Z_bars[i].position.x;
                     pos_z = Z_bars[i].position.z;
                    this.addPlane(new Plane(plane_width, plane_depth, new Vector3(pos_x, pos_y, pos_z), PLANE.YZ));
                }
            }
        }

        for(var i =0; i < Y_bars.length; i++){
            for(var j = i+1; j < Y_bars.length; j++){
                if(Y_bars[i].position.y === Y_bars[j].position.y && Y_bars[i].position.z === Y_bars[j].position.z){
                    if(Y_bars[i].userData.length !== Y_bars[j].userData.length)
                        return;
                    plane_depth = Y_bars[i].userData.length;
                     plane_width = Math.abs(Y_bars[i].position.x - Y_bars[j].position.x) - BAR_DEPTH;
                     pos_x = (Y_bars[i].position.x + Y_bars[j].position.x)/2;
                     pos_y = Y_bars[i].position.y;
                     pos_z = Y_bars[i].position.z;
                    this.addPlane(new Plane(plane_width, plane_depth, new Vector3(pos_x, pos_y, pos_z), PLANE.XY));
                }
                if(Y_bars[i].position.y === Y_bars[j].position.y && Y_bars[i].position.x === Y_bars[j].position.x){
                    if(Y_bars[i].userData.length !== Y_bars[j].userData.length)
                        return;
                    plane_depth = Y_bars[i].userData.length;
                     plane_width = Math.abs(Y_bars[i].position.z - Y_bars[j].position.z) - BAR_DEPTH;
                     pos_z = (Y_bars[i].position.z + Y_bars[j].position.z)/2;
                     pos_x = Y_bars[i].position.x;
                     pos_y= Y_bars[i].position.y;
                    this.addPlane(new Plane(plane_width, plane_depth, new Vector3(pos_x, pos_y, pos_z), PLANE.YZ));
                }
            }
        }
    }

    showProductSize(){
        if(this.Pro_x !== null) this.scene.remove(this.Pro_x);
        if(this.Pro_y !== null) this.scene.remove(this.Pro_y);
        if(this.Pro_z !== null) this.scene.remove(this.Pro_z);
        
        this.Pro_x = this.calcDis(this.product).size[0];
        this.Pro_y = this.calcDis(this.product).size[1];
        this.Pro_z = this.calcDis(this.product).size[2];
        this.scene.add(this.Pro_x);
        this.scene.add(this.Pro_y);
        this.scene.add(this.Pro_z);
    }

    showbarSize(bar){
        if(this.Bar_len !== null) this.scene.remove(this.Bar_len);
        if(this.from_dis !== null) this.scene.remove(this.from_dis);
        if(this.to_dis !== null) this.scene.remove(this.to_dis);

        this.Bar_len = this.calcDis(bar).size[0];
        this.from_dis = this.calcDis(bar).from;
        this.to_dis = this.calcDis(bar).to;
        this.scene.add(this.Bar_len);
        if(this.from_dis !== null) this.scene.add(this.from_dis);
        if(this.to_dis !== null) this.scene.add(this.to_dis);
    }

    calcDis(object){
        let from_dis = null;
        let to_dis = null;
        let size = [];
        let dim0 = new RayysLinearDimension();
        var bbox =  new THREE.Box3().setFromObject(object);
        let from_y = new THREE.Vector3(bbox.max.x, bbox.min.y, bbox.min.z);
        let to_y = new Vector3(bbox.max.x, bbox.max.y, bbox.min.z);
        let dim_y = dim0.create(from_y, to_y, new Vector3(0.1, 0, 0), AXIS.Y);
        let from_x = new Vector3(bbox.min.x, bbox.min.y, bbox.max.z);
        let to_x = new Vector3(bbox.max.x, bbox.min.y, bbox.max.z);
        let dim_x = dim0.create(from_x, to_x, new Vector3(0, 0, 0.2), AXIS.X);
        let from_z = new Vector3(bbox.max.x, bbox.min.y, bbox.min.z);
        let to_z = new Vector3(bbox.max.x, bbox.min.y, bbox.max.z);
        let dim_z = dim0.create(from_z, to_z, new Vector3(0.1, 0, 0), AXIS.Z);

        if(object.axis !== undefined){
            let from_bar = object.getFromBar();
            let to_bar = object.getToBar();

            if(object.place !==PLANE.YZ && object.axis !== AXIS.X){
                let f_x = new Vector3(from_bar.position.x , object.position.y, object.position.z);
                let t_x = new Vector3(to_bar.position.x, object.position.y, object.position.z);
                from_dis = dim0.create(object.position, f_x, new Vector3(0, 0, 0.1), AXIS.X);
                to_dis = dim0.create(object.position, t_x, new Vector3(0, 0, 0.1), AXIS.X);
            }
            if(object.place !==PLANE.XY && object.axis !== AXIS.Z){
                let f_z = new Vector3(object.position.x , object.position.y, from_bar.position.z);
                let t_z = new Vector3(object.position.x, object.position.y, to_bar.position.z);
                from_dis = dim0.create(object.position, f_z, new Vector3(-0.1, 0, 0), AXIS.Z);
                to_dis = dim0.create(t_z, object.position, new Vector3(-0.1, 0, 0), AXIS.Z);
            }
            if(object.place !==PLANE.XZ && object.axis !== AXIS.Y){
                let f_y = new Vector3(object.position.x , from_bar.position.y, object.position.z);
                let t_y = new Vector3(object.position.x, to_bar.position.y, object.position.z);
                from_dis = dim0.create(object.position, f_y, new Vector3(-0.1, 0, 0), AXIS.Y);
                to_dis = dim0.create(t_y, object.position, new Vector3(-0.1, 0, 0), AXIS.Y);
            }
        }
        switch(object.axis){
            case AXIS.X:
                size.push(dim_x);
                break;
            case AXIS.Y:
                size.push(dim_y);
                break;
            case AXIS.Z:
                size.push(dim_z);
                break;
            case undefined:
                size.push(dim_x);
                size.push(dim_y);
                size.push(dim_z);
                break;
            default:
                break;
        }

        return {size : size, from : from_dis, to: to_dis};
    }

    onExportSize(event){
        let rows = [['name','size', 'width', 'depth']];
        for(let index = 0 ; index < this.inner_Group.length; index ++){
            let row = [];
            if(this.inner_Group[index].type === Bar_type.OUTER){
                row.push("OUTER PROFILE");
                row.push((Math.ceil(this.inner_Group[index].userData.length *10))*100);
                row.push(OUTERBAR_WIDTH *1000);
                row.push(BAR_DEPTH *1000);
            }else if(this.inner_Group[index].type === Bar_type.INNER){
                row.push("INNER PROFILE");
                row.push((Math.ceil(this.inner_Group[index].userData.length *10))*100);
                row.push(INNERBAR_WIDTH *1000);
                row.push(BAR_DEPTH *1000);
            }
            rows.push(row);
        }

        this.exportToCsv('size.csv', rows);

    }

    exportToCsv(filename, rows) {
        var processRow = function (row) {
            var finalVal = '';
            for (var j = 0; j < row.length; j++) {
                var innerValue = row[j] === null ? '' : row[j].toString();
                if (row[j] instanceof Date) {
                    innerValue = row[j].toLocaleString();
                };
                var result = innerValue.replace(/"/g, '""');
                if (result.search(/("|,|\n)/g) >= 0)
                    result = '"' + result + '"';
                if (j > 0)
                    finalVal += ',';
                finalVal += result;
            }
            return finalVal + '\n';
        };

        var csvFile = '';
        for (var i = 0; i < rows.length; i++) {
            csvFile += processRow(rows[i]);
        }

        var blob = new Blob([csvFile], { type: 'text/csv;charset=utf-8;' });
        if (navigator.msSaveBlob) { // IE 10+
            navigator.msSaveBlob(blob, filename);
        } else {
            var link = document.createElement("a");
            if (link.download !== undefined) { // feature detection
                // Browsers that support HTML5 download attribute
                var url = URL.createObjectURL(blob);
                link.setAttribute("href", url);
                link.setAttribute("download", filename);
                link.style.visibility = 'hidden';
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
            }
        }
    }


};

export { SceneController };