import * as THREE from 'three';
import { AXIS, INIT_HEIGHT, OUTER_Z_LENGTH, PLANE, COVER_DEPTH, BAR_DEPTH, BASEBAR_WIDTH} from '../Constant.js';

class Cover extends THREE.Mesh {

    constructor(width, height, position, place) {    
        const geometry = new THREE.BoxGeometry( width, height, COVER_DEPTH );
        const material = new THREE.MeshStandardMaterial( {             
            color: 0xffffff,
            emissive: 0x111111,
            metalness: 1,
            roughness: 0.55,  
        });
        super(geometry, material);
        this.position.set(position.x, position.y, position.z);
        this.place = place;
        if(place == PLANE.XY){
            this.rotation.x = 0 ;
        }else if(place == PLANE.XZ){
            this.rotation.x = - Math.PI / 2 ;
        }else if(place == PLANE.YZ){
            this.rotation.y = - Math.PI / 2 ;
        }
        this.userData.width = width;
        this.userData.depth = height;
    }

    scaleOnAxis(scale, axis){
        switch(axis) {
            case AXIS.X:
                if(this.place != PLANE.YZ)
                    this.scale.x =scale/this.userData.width * BASEBAR_WIDTH;
                break;
            case AXIS.Z:
                if(this.place ==PLANE.YZ)
                    this.scale.x = scale/this.userData.width * OUTER_Z_LENGTH;
                if(this.place == PLANE.XZ)
                    this.scale.y = scale/this.userData.height* OUTER_Z_LENGTH;
                break;
            case AXIS.Y:
                if(this.place !=PLANE.XZ)
                    this.scale.y = scale/this.userData.height * (INIT_HEIGHT + 2*BAR_DEPTH);
                break;
        }
    }

    moveOnAxis(value, axis){
        switch(axis) {
            case AXIS.X:
                if(this.place == PLANE.YZ)
                    this.position.x = -value;
                break;
            case AXIS.Z:
                if(this.place == PLANE.XY)
                    this.position.z = -value;
                break;
            case AXIS.Y:
                this.position.y= value;
                break;
        }
    }
}

export { Cover };