import * as THREE from 'three';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
import { AXIS } from '../Constant.js';

class RayysLinearDimension {

    constructor() {

        this.cb = {
            onChange: []
        };
        this.config = {
            headLength: 0.05,
            headWidth: 0.05,
            unitsConverter: function(v) {
                return v;
            }
        };
    }

    create(p0, p1, extrude, axis) {
        
        this.from = p0;
        this.to = p1;
        this.extrude = extrude;
        this.axis = axis;

        this.node = new THREE.Object3D();

        this.node.children.length = 0;

        var pmin = new THREE.Vector3( this.extrude.x + this.from.x, extrude.y + this.from.y, extrude.z + this.from.z);
        var pmax = new THREE.Vector3( this.extrude.x + this.to.x, extrude.y + this.to.y, extrude.z + this.to.z);
        
        var origin = pmax.clone().add(pmin).multiplyScalar(0.5);
        var dir = pmax.clone().sub(pmin);
        dir.normalize();

        var length = pmax.distanceTo(pmin) / 2;
        var hex = 0x0;
        this.arrowHelper0 = new THREE.ArrowHelper(dir, origin, length, hex, this.config.headLength, this.config.headWidth);
        this.node.add(this.arrowHelper0);

        dir.negate();
        this.arrowHelper1 = new THREE.ArrowHelper(dir, origin, length, hex, this.config.headLength, this.config.headWidth);
        this.node.add(this.arrowHelper1);

        const loader = new FontLoader();
        var root = this.node;
        var unit = "mm";
        var axis = this.axis;

        loader.load( 'assets/helvetiker_regular.typeface.json', function ( font ) {

            const matLite = new THREE.MeshBasicMaterial( {
                color: 0x000000,
                transparent: true,
                side: THREE.DoubleSide
            } );
            var  dim = Math.floor(pmax.distanceTo(pmin)*1000);
            if(dim%100 !=0) dim = Math.floor(dim/100) *100;
            const shapes = font.generateShapes( dim + unit, 0.05 );
            const geometry = new THREE.ShapeGeometry( shapes );
            const text = new THREE.Mesh( geometry, matLite );
            text.position.set(origin.x, origin.y, origin.z);
            if(axis == AXIS.X || axis == AXIS.Z) text.rotation.x = - Math.PI/2;

            root.add( text );

        } );

        return this.node;
    }

    update(){

    }

}

export {RayysLinearDimension}