import * as THREE from 'three';
import { AXIS, BAR_DEPTH, INNERBAR_WIDTH, OUTERBAR_WIDTH, PLANE, Bar_type, DELTA_DOT, INIT_Y_DOWN, Bar_Group, Direction} from "../Constant.js";
import { Bar } from "./Bar.js";
import { SceneController } from '../SceneController.js';

class OuterBar extends Bar {
    constructor(length, type, material, position, axis, direction, place, group, parent) {
        const width = (type === Bar_type.INNER) ? INNERBAR_WIDTH : OUTERBAR_WIDTH ;
        const depth = BAR_DEPTH;
        let rotation = -Math.PI / 2 ; 
        const geometry = new THREE.BoxGeometry(width, length, depth);
        super(material, geometry, position, axis, direction);

        switch(axis){
            case AXIS.X:
                this.rotation.z = rotation;
                this.start_pos = position.x- length/2;
                 break;
            case AXIS.Y:
                this.start_pos = position.y - length/2;
                break;
            case AXIS.Z:
                this.rotation.x = rotation;
                this.start_pos = position.z - length/2;
                break;
            default:
                break;
        }
        this.type = type;
        this.place = place;
        this.userData.length = length;
        this.group = group;
        this.userData.start = null;
        this.userData.end = null;
        this.userData.from = null;
        this.userData.to = null;
        this.userData.connect = [];
        this.isMovable = false;
        this.node = parent; 
        this.drawDots(length);      
    }

    removeDots(){
        for(let index = 0; index < this.children.length; index++){
            this.remove(this.children[index]);
        }
    }

    drawDots(length){
        this.removeDots();
        let dots_num = Math.floor(length*10);
            //temp////
        if(this.axis == AXIS.Z) dots_num = dots_num + 1;
        //temp////
        let circle_geo = new THREE.CircleGeometry( 0.0025, 128 );
        let circle_mat = new THREE.MeshBasicMaterial( { color: 0x000000 , side : THREE.DoubleSide} );
        for(let index = 0 ; index < dots_num - 1; index++){
            this.setDotpos(circle_geo, circle_mat, length, index, "Z_plus");
            this.setDotpos(circle_geo, circle_mat, length, index, "Z_minus");
            this.setDotpos(circle_geo, circle_mat, length, index, "X_plus");
            this.setDotpos(circle_geo, circle_mat, length, index, "X_minus");
        }
    }

    setDotpos(geometry, material, length, index, direction){
        let circle = new THREE.Mesh( geometry, material );
        circle.position.y = (-length/2 + (index + 1)* 0.1)/this.scale.y;
        let z_pos, x_pos;
        z_pos = x_pos = BAR_DEPTH/2 + DELTA_DOT;
        if(this.type == Bar_type.INNER)   x_pos = INNERBAR_WIDTH/2 + DELTA_DOT;
        switch(direction){
            case "Z_plus":
                circle.position.z = z_pos;
                break;
            case "Z_minus":
                circle.position.z = - z_pos;
                break;
            case "X_plus":
                circle.position.x = x_pos;
                circle.rotation.y = Math.PI/2;
                break;
            case "X_minus":
                circle.position.x = -x_pos;
                circle.rotation.y = Math.PI/2;
                break;
            default:
                break
        }
        this.add(circle);
    }

    setBar(axis, length,  center, place, bar_set, direction){
        this.scale.y = length / this.userData.length;

        switch(axis){
            case AXIS.X:
                this.position.x = center;
                if(place == PLANE.XZ) {
                    this.rotation.x =  -Math.PI / 2 ; 
                    this.position.z = this.resetPos(this.position.z)
                    if(bar_set === "double"){
                        if(direction === Direction.START)
                            this.position.z = this.position.z - INNERBAR_WIDTH/2
                        else if(direction === Direction.END)
                            this.position.z = this.position.z + INNERBAR_WIDTH/2
                    }
                }else if(place == PLANE.XY){
                    this.position.y = this.resetPos(this.position.y - INIT_Y_DOWN) +INIT_Y_DOWN
                    if(bar_set === "double"){
                        if(direction === Direction.START)
                            this.position.y = this.position.y - INNERBAR_WIDTH/2
                        else
                            this.position.y = this.position.y + INNERBAR_WIDTH/2
                    }         
                }
                break;
            case AXIS.Y:
                this.position.y = center;
                if(place == PLANE.XY){
                    this.position.x = this.resetPos(this.position.x);
                    if(bar_set === "double"){
                        if(direction === Direction.START)
                            this.position.x = this.position.x - INNERBAR_WIDTH/2
                        else
                            this.position.x = this.position.x + INNERBAR_WIDTH/2
                    }
                }else if(place == PLANE.YZ){
                    this.rotation.y =  -Math.PI / 2 ; 
                    this.position.z = this.resetPos(this.position.z);
                    if(bar_set === "double"){
                        if(direction === Direction.START)
                            this.position.z = this.position.z - INNERBAR_WIDTH/2
                        else
                            this.position.z = this.position.z + INNERBAR_WIDTH/2
                    }
                }
                break;
            case AXIS.Z:
                this.position.z = center;
                if(place == PLANE.YZ) {
                    this.rotation.y =  -Math.PI / 2 ; 
                    this.position.y = this.resetPos(this.position.y - INIT_Y_DOWN) +INIT_Y_DOWN;
                    if(bar_set === "double"){
                        if(direction === Direction.START)
                            this.position.y = this.position.y - INNERBAR_WIDTH/2
                        else if(direction === Direction.END)
                            this.position.y = this.position.y + INNERBAR_WIDTH/2
                    }
                }else if(place == PLANE.XZ){
                    this.position.x = this.resetPos(this.position.x);
                    if(bar_set === "double"){

                        if(direction === Direction.START)
                            this.position.x = this.position.x - INNERBAR_WIDTH/2
                        else if(direction === Direction.END)
                            this.position.x = this.position.x + INNERBAR_WIDTH/2
                    }
                }
                break;
        }
    }

    resetPos(value){
        let update_pos = Math.floor(value* 10)/10;
        return update_pos;
    }

    removeUserdata(){
        this.removeAllchild();
        this.getStartBar().removeChildBar(this);
        this.getEndbar().removeChildBar(this);
        this.node.remove(this);
        const sceneController = SceneController.instance();
        for(let index = 0; index < sceneController.inner_Group.length; index++){
            if(sceneController.inner_Group[index] === this)
                sceneController.inner_Group.splice(index, 1);
        }
    }

    addConnectchild(childbar){
        this.userData.connect.push((childbar));
    }

    removeChildBar(childbar){
        for(let index = 0 ; index < this.userData.connect.length; index ++){
            if(childbar === this.userData.connect[index]){
                this.userData.connect.splice(index, 1);
            }
        }
    }

    removeAllchild(){
        for(let index = 0 ; index < this.userData.connect.length; index ++){
            this.node.remove(this.userData.connect[index]);
        }
    }

    setParentBar(startbar, endbar){
        this.userData.start = startbar;
        this.userData.end = endbar;
    }

    getStartBar(){
        return this.userData.start;
    }

    getEndbar(){
        return this.userData.end;
    }

    getMovable(){
        return this.isMovable;
    }

    checkMovable(){
        if(this.userData.connect.length ==0 && this.group != Bar_Group.SET)
            this.isMovable = true;
        else
            this.isMovable = false;
    }

    setRangeBar(frombar, tobar){
        this.userData.from = frombar;
        this.userData.to = tobar;
    }

    getFromBar(){
        return this.userData.from;
    }

    getToBar(){
        return this.userData.to;
    }

    getLength(){
        return this.userData.length * this.scale.y;
    }

    getSidePos(){
        let start_pos, end_pos;
        switch(this.axis){
            case AXIS.X:
                start_pos = this.position.x - this.getLength()/2
                end_pos = this.position.x + this.getLength()/2;
                break;
            case AXIS.Y:
                start_pos = this.position.y - this.getLength()/2
                end_pos = this.position.y + this.getLength()/2;
                break;
            case AXIS.Z:
                start_pos = this.position.z - this.getLength()/2
                end_pos = this.position.z + this.getLength()/2;
                break;
            default:
                break;
        }

        return { start: start_pos, end: end_pos};
    }

    setX_LimitPos(){
        if(this.position.x <= this.getFromBar().position.x + INNERBAR_WIDTH){
            this.position.x = this.getFromBar().position.x + INNERBAR_WIDTH;  
        }
        if(this.position.x >= this.getToBar().position.x - INNERBAR_WIDTH){
            this.position.x = this.getToBar().position.x - INNERBAR_WIDTH;
        }
    }

    setY_LimitPos(){
        if(this.position.y <= this.getFromBar().position.y + INNERBAR_WIDTH){
            this.position.y = this.getFromBar().position.y + INNERBAR_WIDTH;  
        }
        if(this.position.y >= this.getToBar().position.y - INNERBAR_WIDTH){
            this.position.y = this.getToBar().position.y - INNERBAR_WIDTH;
        }
    }

    setZ_LimitPos(){
        if(this.position.z <= this.getFromBar().position.z + INNERBAR_WIDTH){
            this.position.z = this.getFromBar().position.z + INNERBAR_WIDTH;  
        }
        if(this.position.z >= this.getToBar().position.z - INNERBAR_WIDTH){
            this.position.z = this.getToBar().position.z - INNERBAR_WIDTH;
        }
    }

}

export { OuterBar };