import * as THREE from 'three';
import { PLANE, COVER_DEPTH } from '../Constant.js';

class CableBox extends THREE.Group {
    constructor( position, place) {  
        super();
        const material = new THREE.MeshStandardMaterial( {             
            color: 0xffffff,
            emissive: 0x111111,
            metalness: 1,
            roughness: 0.55,  
        });
        const width= 0.2;
        const height = 0.295;
        const depth = 0.248;
        let shape = new THREE.Shape();
        let value = 2/(3*Math.sqrt(3));
        shape.moveTo(-height/2, 0);
        shape.lineTo(-height/2, width/3);
        shape.lineTo(-(height/2- value *width), width);
        shape.lineTo(height/2-value *width, width);
        shape.lineTo(height/2, width/3);
        shape.lineTo(height/2, 0);
        shape.lineTo(-height/2, 0);
        const extrudeSettings = {
            steps: 2,
            depth: 0.2,
            bevelEnabled: false,
            bevelThickness: 0.1,
            bevelSize: 0.1,
            bevelOffset: 0,
            bevelSegments: 1
        };
        let bevel_geo = new THREE.ExtrudeGeometry( shape, extrudeSettings );
        const mesh_plate = new THREE.Mesh( bevel_geo, material );
        this.rotation.z = -Math.PI/2;
        this.add(mesh_plate);
        this.position.set(position.x, position.y, position.z-depth/2);
    //     let poly_width;
    //     poly_width = width * 4/(3*Math.sqrt(3));
    //    let poly_geo = new THREE.BoxGeometry( depth, poly_width, 0.002 );
    //    let poly_mat = new THREE.MeshBasicMaterial( { color : 0x000000, side : THREE.DoubleSide });
    //    const mesh_poly  = new THREE.Mesh( poly_geo, poly_mat );
    //    mesh_poly.position.x = - height/2;
    //    mesh_poly.position.y = 2* width/3;
    //     mesh_poly.rotation.y = Math.PI/2;
    //    this.add(mesh_poly);
    }
}

export { CableBox };